<script>
    import {onMount} from 'svelte';
    import {fade} from 'svelte/transition';
    import {firebase, db} from './../firebase.js';
    import L from 'leaflet';

    const MY_ACCESS_TOKEN = 'pk.eyJ1IjoiZGFwcGRldnYiLCJhIjoiY2s3ZWo0dWV4MDYzcjNncHRncnlvdHY2OSJ9.zLk9G0tHK9iU-onN_TjxFw';

    // MAPBOX SDK
    import mbxGeocoding from '@mapbox/mapbox-sdk/services/geocoding';
    import mbxClient from '@mapbox/mapbox-sdk';
    const geocodingClient = mbxGeocoding({ accessToken: MY_ACCESS_TOKEN });

    // Map variables
    let map, mapCenter, LatLng;
    let serviceMarker, mapIcon, locationIcon;

    // Map info
    let region, city, district;

    let markerLocation = {
        latlng: null,
        region: 'none',
        city: 'none',
        district: 'none'
    };

    let priceList;

    onMount(mapInit)

    let getUser;
    let getUserAccount;

    firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
            getUser = user;
        } else {
            console.log('no user login')
        }
    });

    function mapInit() {

        // MAP
        map = L.map('map', {
            zoomControl: false
        })
        .setView([55.80046084146819, 49.105174541473396], 13);

        // Get user location
        map.locate({watch: false, setView: true, enableHighAccuracy: true});

        L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}', {
            attribution: '<span style="font-size: smaller;"> Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a>, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery © <a href="https://www.mapbox.com/">Mapbox</a></span>',
            maxZoom: 19,
            id: 'mapbox/streets-v11',
            tileSize: 512,
            zoomOffset: -1,
            accessToken: MY_ACCESS_TOKEN
        }).addTo(map);

        // Map center
        mapCenter = map.getCenter();

        // Zoom control
        L.control.zoom({position: 'bottomright'}).addTo(map);

        // Icon init
        mapIcon = L.Icon.extend({
            options: {
                iconSize:     [50, 50], // size of the icon
                iconAnchor:   [25, 25], // point of the icon which will correspond to marker's location
                popupAnchor:  [0, -25] // point from which the popup should open relative to the iconAnchor
            }
        })

        locationIcon = new mapIcon({iconUrl: './icons/location.png'});
        
        // Events
        map.on('locationfound', setServiceMarker);
        map.on('click', setMarkerLocation);
        
    }

    function setServiceMarker(e) {
        
        serviceMarker = L.marker(e.latlng, {icon: locationIcon, draggable: true}).addTo(map);
        
        serviceMarker.on('move', updateMarkerLocation);
        serviceMarker.on('moveend', getCurrentRegion);

    }

    function updateMarkerLocation(e) {
        markerLocation.latlng = e.latlng;
    }

    function setMarkerLocation(e) {
        serviceMarker.setLatLng(e.latlng);
        markerLocation.latlng = e.latlng;
        getCurrentRegion()
    }

    // Get current region info
    function getCurrentRegion() {

        let ml = markerLocation.latlng;

        geocodingClient.reverseGeocode({
        query: [ml.lng, ml.lat]
        })
        .send()
        .then(response => {
            const result = response.body;
            const resarr = result.features;

            resarr.forEach(el => {
                if (el.place_type[0] === 'region') {
                    region = el.text;
                }
                else if (el.place_type[0] === 'district') {
                    district = el.text;
                }
                else if (el.place_type[0] === 'place') {
                    city = el.text;
                }
            });

            let hasDistrict = resarr.some(el => {
                return el.place_type[0] === 'district';
            });

            if (!hasDistrict) {
                district = city;
            }
            
            markerLocation.region = region;
            markerLocation.city = city;
            markerLocation.district = district;

            console.log(markerLocation)
        });
    }

    
    let serviceName;
    let serviceType;
    let serviceOffer;
    let servicePhone = "+7";

    let wHours = {
        alltime: false,
        saturday: false,
        sunday: false,
        weekdays: [],
        sat: [],
        sun: []
    }

    // ADD NEW SERVICE
    function checkBalance() {

        let price;

        const priceList = db.collection('priceList').doc('addService');
        priceList.get()
        .then(function(doc) {
            const data = doc.data();
            price = data[serviceType];
        })
        .catch(function(error) {
            alertBoxCall(`Ошибка: ${error}`);
        });

        let balance;

        const user = db.collection('users').doc(getUser.uid);
        user.get()
        .then(function(doc) {
            const data = doc.data();
            balance = data.balance;

            if ((balance - price) > 0) {

                addService(price);

            } else {

                alertBoxCall(`Недостаточно средств.`);
            }
            
        })
        .catch(function(error) {
            alertBoxCall(`Ошибка: ${error}`);
        });
    }

    // ADD NEW SERVICE
    function addService(price) {

        if (markerLocation.region && markerLocation.city != 'none') {

            if (serviceOffer && serviceName && serviceType != undefined) {
    
                const services = db.collection('services');
                services.add({
                    region: markerLocation.region,
                    district: markerLocation.district,
                    city: markerLocation.city,
                    name: serviceName,
                    type: serviceType,
                    offer: serviceOffer,
                    phone: servicePhone,
                    location: [markerLocation.latlng.lat, markerLocation.latlng.lng],
                    owner: getUser.uid,
                    time: {wHours}
                })
                .then(function(docRef) {
                    balanceDecrement(price);
                    alertBoxCall('Новый сервис добавлен. Данные обновятся через несколько минут.');
                    
                })
                .catch(function(error) {
                    alertBoxCall(`Ошибка: ${error}`);
                });
            } else {
                alertBoxCall('Заполните все поля.');
            }
        }
        else {
            
            alertBoxCall('Установите маркер на местоположение сервиса.');
        }
        
    }

    let alertBox;
    let alertBoxContent;

    // ALERT BOX
    function alertBoxCall(mes) {

        if (!alertBox) {

            alertBoxContent = mes;
            alertBox = true;
        }
        setTimeout(()=>{
            alertBox = false;
        }, 5000)
    }
    
    // Cloud Functions
    let balanceDecrementFunction = firebase.functions().httpsCallable('balanceDecrement');

    // Call cloud functions
    function balanceDecrement(price) {
        
        balanceDecrementFunction({amount: price})
        .then(function(result) {
            console.log(result)
        }).catch(function(error) {
            console.log(error.code, error.message, error.details)
        });
    }

</script>

<div class="mt-16">

    <!-- MAP -->
    <div id="map">
    </div>

    <!-- NEW SERVICE -->
    <div class="p-2 bg-white rounded-lg shadow-lg w-full">

        <div>
            <h1 class=" font-semibold text-center">
                Добавить сервис
            </h1>
        </div>
        
        <div class="mt-2">
            <label for="service-type" class=" text-gray-700 text-sm font-semibold">
            Тип сервиса:
            </label>
            <select name="service-type" id="service-type" bind:value={serviceType} class="border rounded-md"> 
                <option value=""></option>
                <option value="СТО">СТО</option>
                <option value="Мелкосрочка">Мелкосрочка (частный гараж)</option>
                <option value="Шиномонтаж">Шиномонтаж</option>
                <option value="Автомойка">Автомойка</option>
                <option value="Автозапчасти">Автозапчасти</option>
                <option value="Автостекло/Тонировка">Автостекло/Тонировка</option>
                <option value="Автозаправка">Автозаправка</option>
                <option value="ГСМ">ГСМ</option>
                <option value="Авторазбор">Авторазбор</option>
                <option value="Кафе">Придорожное кафе</option>
                <option value="Отель">Придорожный отель</option>
            </select>
        </div>

        <div>
            <p>
                Название:
                <input type="text" placeholder="Название сервиса" maxlength="50" bind:value={serviceName}>
            </p>
        </div>

        <div>
            <p>
                Виды услуг:
                <input type="text" placeholder="Короткое описание" maxlength="150" bind:value={serviceOffer}>
            </p>
        </div>

        <div>
            <p>Время работы:</p>
            <p>
                Круглосуточно 
                <input type="checkbox" bind:checked={wHours.alltime}>
            </p>
            <p>Пн - Пт</p>
            <p>
                С
                <input type="time" bind:value={wHours.weekdays[0]}>
                до
                <input type="time" bind:value={wHours.weekdays[1]}>
            </p>
            <p>Суббота</p>
            <p>
                С
                <input type="time" bind:value={wHours.sat[0]}>
                до
                <input type="time" bind:value={wHours.sat[1]}>
                Выходной 
                <input type="checkbox" bind:checked={wHours.saturday}>
            </p>
            <p>Вокресенье</p>
            <p>
                С
                <input type="time" bind:value={wHours.sun[0]}>
                до
                <input type="time" bind:value={wHours.sun[1]}>
                Выходной 
                <input type="checkbox" bind:checked={wHours.sunday}>
            </p>
        </div>

        <div>
            <p>
                Номер телефона: 
                <input type="text" bind:value={servicePhone}>
            </p>
        </div>

        <div class="mt-2 text-center">
            <button class="btn-white" on:click={()=> priceList = true}>
                Стоимость размещения 
            </button>
            <button class="btn-green" on:click={()=> checkBalance()}>
                Добавить 
            </button>
        </div>

        
    </div>
</div>

<!-- PRICE LIST -->
{#if priceList}
    <div id="priceList" class=" fixed top-0">

        <div transition:fade="{{ duration: 500 }}" class="p-2 border bg-white rounded-lg shadow-lg">

            <p class="text-center text-lg font-semibold">
                Стоимость размещения
            </p>

            <p>
                100 руб.: Мелкосрочка (частный гараж), Шиномонтаж, Автостекло/Тонировка.
            </p>
            <p>
                300 руб.: Автозапчасти, Автомойка, Авторазбор, СТО, ГСМ.
            </p>
            <p>
                500 руб.: Придорожное кафе, Придорожный отель.
            </p>
            <p>
                1000 руб.: Автозаправка.
            </p>
            <p>
                Стоимость размещения оплачивается единоразово и списывается с баланса аккаунта.
            </p>
            <p class="mt-2 text-center">
                <button class="btn-gray" on:click={() => priceList = false}> Закрыть </button>
            </p>

        </div>

    </div>
{/if}

<!-- ALERT BOX -->
{#if alertBox}
    <div id="alertBox" class=" fixed top-0">

        <div transition:fade="{{ duration: 500 }}" class="p-3 border bg-white rounded-lg shadow-lg">

            <p class="text-center">
                {alertBoxContent}
            </p>

            <p class="mt-2 text-center">
                <button class="btn-orange" on:click={() => alertBox = false}>Ок</button>
            </p>

        </div>

    </div>
{/if}

<style>#map
    {
  height: 300px;
  width: 100%;
  z-index: 0;
}

#alertBox {
  width: 240px;
  height: 140px;
  left: 50%;
  top: 50%;
  margin-left:-120px;
  margin-top:-70px;
}

#priceList {
  width: 300px;
  height: 200px;
  left: 50%;
  top: 50%;
  margin-left:-150px;
  margin-top:-100px;
}

p 
    {
  margin: 0.25rem;
  font-size: 0.875rem;
  font-weight: 600;
  color: #4a5568;
}

