<script>
    import {firebase, db, googleProvider} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import { createEventDispatcher } from 'svelte';

    export let region;
    export let city;

    // AUTH USER and DB
    let getUser;
    let getUserAccount;

    firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
            // User is signed in.
            getUser = user;
        } else {
            // No user is signed in.
            console.log('no user login')
        }
    });

    let reviewText;
    let rating;

    const dispatch = createEventDispatcher();

    let activeOrders = (ref) => ref.where("clientUID", "==", getUser.uid).where("active", "==", true);
    let completedOrders = (ref) => ref.where("clientUID", "==", getUser.uid).where("completed", "==", true);

 
    // function nextOrders(last) {
    //     queryOrders = (ref) => ref.orderBy("date", "desc").startAfter(last.date).limit(3);
    // }

    function cancelOrder(ref, order) {

        db.collection(ref).doc(order.id)
        .delete().then(function() {
            dispatch('alert', {message: "Заказ отменен!"});
        }).catch(function(error) {
            console.error("Error removing document: ", error);
        });
    }

    function playSoundBeBe() {
        let audio = new Audio('./sounds/car_horn_beep_x3_short.mp3');
        audio.play();
    }

    function checkOnWay(ref, order) {

        setTimeout(function check() {

            db.collection(ref).doc(order.id)
            .get()
            .then(function(doc) {
                const data = doc.data();
                const onwaytrue = data.onway;

                if (!onwaytrue) {

                    dispatch('checkOnWay', {ref, order});
                    setTimeout(check, 5000);
                }
            })
        }, 5000)
    }

    function findDriverLocation(uid) {
        db.collection('taxiDrivers').doc(uid)
        .get({source: 'server'})
        .then(function(doc) {
            let coors = doc.data().location;
            dispatch('findDriverLocation', {
                location: coors
            });
            dispatch('close');
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
    }

    function acceptOrder(ref, order) {
        db.collection(ref).doc(order.id).update({
            accepted: true
        })
        .then(function() {
            checkOnWay(ref, order);
            dispatch('alert', {message: "Водитель оповещён."});
        })
        .catch(function(error) {
            // The document probably doesn't exist.
            console.error("Error updating document: ", error);
        });
    }

    function setReview(rating, ref, order) {
        db.collection(ref).doc(order.id).update({
            rating: rating
        })
        .then(function() {
            db.collection(`taxiDrivers/${order.setDriver}/ratings/`).add({
                date: firebase.firestore.FieldValue.serverTimestamp(),
                rating: rating,
                review: reviewText
            })
            .then(function() {

                ('alert', {message: 'Спасибо.'});
            })
            .catch(function(error) {
                // The document probably doesn't exist.
                console.error("Error updating document: ", error);
            });
        })
        .catch(function(error) {
            // The document probably doesn't exist.
            console.error("Error updating document: ", error);
        });
    }

</script>

<div class="p-2 w-full z-0 bg-white">
    <FirebaseApp {firebase}>
        <User let:user>
            <Collection 
            path={`taxiOrders/region/${region}/city/${city}`} 
            query={(ref) => ref.where("clientUID", "==", user.uid)}
            let:data={orders} 
            let:ref>

                <div class="w-full">

                    <!-- Header -->
                    <div>
                        <p class="font-semibold text-center"> 
                        Текущие заказы: 

                        <span class=" float-right">
                            <img src="./icons/close.svg" alt="x" class="cursor-pointer"
                            on:click={()=> dispatch('close')}>
                        </span>
                        </p>
                        <hr>
                    </div>

                    {#if orders.length == 0}
                    <div>
                        <p class="text-center italic">
                            Заказов нет
                        </p>
                    </div>
                    {/if}

                    <!-- ORDERS -->
                    <div>
                    {#each orders as order}
                        {#if !order.payed}

                        <!-- ORDER -->
                        <div>

                            <!-- HEADER -->
                            <div class="mt-2 text-sm">

                                <!-- SET DRIVER -->
                                {#if order.setDriver && order.onway != true}
                                    <Doc path={`taxiDrivers/${order.setDriver}`} let:data on:data={playSoundBeBe}>
                                        <div>
                                            <p class="font-semibold text-base text-green-700">
                                            <span class=" font-normal text-gray-800"> Заказ принял: </span> 
                                            {data.name} {data.lastName}
                                            </p>

                                            <p class="font-semibold text-base text-orange-600"> 
                                            <span class=" font-normal text-gray-800"> Автомобиль: </span>
                                            {data.carBrand} {data.carNumber}
                                            </p>

                                            <div class="mt-2 content-center">
                                                <img class="w-full h-32 object-cover object-none object-center" src="{data.driverAuto.frontside}" alt="auto">
                                            </div>

                                            <!-- Driver buttons -->
                                            <div class="mt-2 flex flex-row justify-around">        
                                                <div>
                                                    <button class="order-btn bg-teal-400" on:click={findDriverLocation(order.setDriver)}>
                                                        <img class="w-8 h-8" src="./icons/place.svg" alt="placeMarker">
                                                    </button>
                                                </div>
                                                <div>
                                                    <button class="order-btn ml-4 bg-green-400">
                                                        <a href="tel:{data.phone}" alt="phone">
                                                            <img class="w-8 h-8" src="./icons/phone.svg" alt="phone">
                                                        </a>
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                    </Doc>
                                {/if}

                                <!-- driver arrived -->
                                {#if order.driverArrived && order.onway != true}
                                    <p class="mt-4 font-semibold text-orange-600 text-lg text-center" on:load={playSoundBeBe()}>
                                    Водитель подъехал и ждёт вас!
                                    </p>
                                {/if}

                                {#if order.driverArrived && order.accepted != true}
                                    <p class="text-center">
                                        <button class="btn-orange" on:click={acceptOrder(ref.path, order)}>
                                            Выхожу
                                        </button>
                                    </p>
                                {/if}

                                {#if order.accepted && order.onway != true}
                                    <p class="text-center">
                                        <button class="btn-green" on:click={()=> order.ref.update({onway: true})}>
                                            Поехали
                                        </button>
                                    </p>
                                {/if}

                                <!-- ORDER DETAILS -->
                                <div class="mt-2">
                                    <p>
                                    <span class="font-semibold"> Откуда: </span>  {order.address} 
                                    </p>
                                    <p>
                                    <span class="font-semibold"> Куда: </span> {order.routes} 
                                    </p>
                                    <p>
                                    <span class="font-semibold"> Примечание: </span> {order.desc} 
                                    </p>
                                    <p>
                                    <span class="font-semibold text-orange-600"> Цена:  {order.price} руб.</span>
                                    </p>
                                    <p>
                                    <span class="font-semibold text-green-500"> Способ оплаты:  {order.paymentMethod}</span>
                                    </p>
                                </div>

                            </div>

                            <!-- BUTTONS -->
                            <div class="mt-2 mb-2 text-center">

                                {#if order.driverArrived != true}
                                <button class="px-2 text-white bg-red-600 shadow-lg" on:click={cancelOrder(ref.path, order)}>
                                    Отменить заказ
                                </button>
                                {/if}
                            </div>

                            <hr>

                            <!-- RATING & REVIEW -->
                            <div class="my-2 text-center">
                                {#if order.completed && order.rating == undefined}

                                <p class=" font-semibold text-center text-lg">
                                Оцените поездку:
                                </p>
                                
                                <div class="rating-container">
                                    <div class="star-container">
                                        {#if rating >= 1}
                                            <img src="./icons/star.svg" alt="star" class="star" on:click={()=> rating = 1}>
                                        {:else}
                                            <img src="./icons/star_border.svg" alt="star" class="star" on:click={()=> rating = 1}>
                                        {/if}
                                        {#if rating >= 2}
                                            <img src="./icons/star.svg" alt="star" class="star" on:click={()=> rating = 2}>
                                        {:else}
                                            <img src="./icons/star_border.svg" alt="star" class="star" on:click={()=> rating = 2}>
                                        {/if}
                                        {#if rating >= 3}
                                            <img src="./icons/star.svg" alt="star" class="star" on:click={()=> rating = 3}>
                                        {:else}
                                            <img src="./icons/star_border.svg" alt="star" class="star" on:click={()=> rating = 3}>
                                        {/if}
                                        {#if rating >= 4}
                                            <img src="./icons/star.svg" alt="star" class="star" on:click={()=> rating = 4}>
                                        {:else}
                                            <img src="./icons/star_border.svg" alt="star" class="star" on:click={()=> rating = 4}>
                                        {/if}
                                        {#if rating == 5}
                                            <img src="./icons/star.svg" alt="star" class="star" on:click={()=> rating = 5}>
                                        {:else}
                                            <img src="./icons/star_border.svg" alt="star" class="star" on:click={()=> rating = 5}>
                                        {/if}
                                    </div>

                                    <div class="mt-1">
                                        <input type="text" placeholder="Ваш отзыв" bind:value={reviewText} class="border rounded-lg">
                                    </div>
                                    <div class="mt-2">
                                        <button class="btn-orange" on:click={()=> setReview(rating, ref.path, order)}>Отправить</button>
                                    </div>
                                </div>

                                {/if}
                                <hr>
                            </div>
                            
                        </div>
                        {/if}
                    {/each}
                    </div>
                    
                </div>
    
                <div slot="fallback">
                    Невозможно загрузить данные...
                </div>

            </Collection>
        </User>
    </FirebaseApp>
</div>

<style>.rating-container
    {
  margin: 0.25rem;
  padding: 0.5rem;
  border-width: 1px;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  text-align: center;
  background-color: #fff;
}

.star-container
    {
  padding: 0.5rem;
  text-align: center;
}

.star
    {
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
}

.order-btn 
    {
  margin-left: 0.5rem;
  padding: 0.5rem;
  border-radius: 9999px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  outline: 0;
}

/*# sourceMappingURL=data:application/json;base64, */</style>