<script>
    import {firebase, db} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import { createEventDispatcher, onMount } from 'svelte';

    export let region;
    export let getUserAccount;

    const dispatch = createEventDispatcher();

    let message = '';
    let phone = getUserAccount.phone;

    // Cloud Functions
    let balanceDecrementFunction = firebase.functions().httpsCallable('balanceDecrement');

    // Call cloud functions
    function balanceDecrement(opt) {

        let amount;

        if (opt === 'life') {
            amount = 1000;
        }
        else if (opt === 'medical') {
            amount = 500;
        }
        else if (opt === 'technical') {
            amount = 100;
        }
        
        balanceDecrementFunction({amount})
        .then(function(result) {
            console.log('balanceDecrement', result)
        }).catch(function(error) {
            console.log(error.code, error.message, error.details)
        });
    }

</script>

<div class="p-1 w-full bg-gray-900 rounded-lg shadow-lg">
    <FirebaseApp {firebase}>
        <User let:user>
            <Collection path={`icons/sos/${region}`} query={ (ref) => ref.where("author", "==", user.uid)} let:data let:ref>
                {#if data.length >= 1}
                    {#each data as d}
                        <div class="p-2 bg-white rounded-lg">
                            <div>
                                <p>
                                    {d.message}
                                </p>
                            </div>
                            
                            <div class="text-center">
                                <button on:click={()=> d.ref.delete()}> Отменить </button>
                            </div>
                        </div>
                    {/each}
                {:else}
                    <div>
                        <div class="header">
                            <p class=" text-center text-white font-semibold">
                                Что случилось?
                            </p>
                        </div>
                        <div class="help-block">

                            <p class="m-2 font-semibold">
                                На вас напали
                            </p>
                            
                            <input type="text" bind:value={phone}>
                            
                            <input type="text" bind:value={message} placeholder="Опишите проблему">

                            <button class="m-1 p-1 bg-red-600 text-white" on:click={()=> dispatch('danger', {message, type: 'life', phone})} 
                            on:click={()=> balanceDecrement('life')}>
                                Угроза жизни
                            </button>

                            <p class=" text-sm text-red-600">
                                При нажатии на кнопку с баланса спишется 1000 руб.
                            </p>
                        </div>

                        <div class="help-block">

                            <p class="m-2 font-semibold">
                                ДТП или требуется медицинская помощь
                            </p>
                            
                            <input type="text" bind:value={phone}>

                            <input type="text" bind:value={message} placeholder="Опишите проблему">

                            <button class="m-1 p-1 bg-red-600 text-white" on:click={()=> dispatch('danger', {message, type: 'medical', phone})}
                            on:click={()=> balanceDecrement('medical')}>
                                Мед. помощь
                            </button>

                            <p class=" text-sm text-red-600">
                                При нажатии на кнопку с баланса спишется 500 руб.
                            </p>
                        </div>

                        <div class="help-block">

                            <p class="m-2 font-semibold">
                                Требуется техническая помощь (поломка авто)
                            </p>

                            <input type="text" bind:value={phone}>

                            <input type="text" bind:value={message} placeholder="Опишите проблему">

                            <button class="m-1 p-1 bg-gray-800 text-white" on:click={()=> dispatch('danger', {message, type: 'technical', phone})}
                            on:click={()=> balanceDecrement('technical')}>
                                Тех. помощь
                            </button>

                            <p class=" text-sm text-red-600">
                                При нажатии на кнопку с баланса спишется 100 руб.
                            </p>
                        </div>

                        <div class="mt-2 text-center">
                            <button class="p-1 bg-orange-600 text-white rounded-lg" on:click={()=> dispatch('close')}> Отмена </button>
                        </div>
                    </div>
                {/if}
            </Collection>
        </User>
    </FirebaseApp>
</div>

<style>.help-block {
  margin-top: 0.5rem;
  padding: 0.5rem;
  background-color: #fff;
  border-radius: 0.5rem;
  text-align: center;
}

input {
  border-width: 1px;
}

/*# sourceMappingURL=data:application/json;base64, */</style>