<script>
    import { writable } from 'svelte/store';
    import {firebase, db, googleProvider} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import { createEventDispatcher } from 'svelte';

    export let region;
    export let district;
    export let city;

    const dispatch = createEventDispatcher();

    let message = {
        date: firebase.firestore.FieldValue.serverTimestamp(),
        from: null,
        name: null,
        to: 'all',
        text: ' ',
    }

    let messageTo = 'all';

    let mesLimit = 10000;

    let nowMilli;
    let lastMes = new Date().getTime() - mesLimit;

    function sendMessage(user) {
        nowMilli = new Date().getTime();

        if ((nowMilli - lastMes) > mesLimit) {

            message.from = user.uid;
            message.name = user.displayName;
            message.to = messageTo;
    
            db.collection(`chat/region/${region}/city/${city}`).add(message)
            .then(function(docRef) {
                lastMes = new Date().getTime();
                messageTo = 'all'
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
            });
        } else {
            alert('Действует ограничение, 1 сообщение/10 секунд.')
        }
    }  

</script>


    <FirebaseApp {firebase}>
        <User let:user>

                <div class="taxi-orders-container z-0 fixed ml-12 mr-2">

                    <!-- MESSAGES -->
                    <div id="messages" class="p-2 bg-gray-200 rounded-lg shadow-lg h-80 w-full overflow-y-auto">
                    
                        <div class=" p-1 text-sm">
                            {city} 
                            {#if district != city}
                                {district}
                            {/if}
                        </div>

                        <Collection path={`chat/region/${region}/city/${city}`} 
                        query={ (ref) => ref.orderBy("date", "desc").limit(30) }
                        let:data={messages} 
                        let:ref>

                            <ul>

                            <!-- MESSAGE -->
                            {#each messages as mes}
                                {#if mes.date != null || undefined}
                                <li>
                                    <div class="mt-2">
                                        <p class="text-gray-700">
                                        <span class="font-semibold cursor-pointer" on:click={() => messageTo = mes.name}>

                                        {mes.name}:

                                        </span>

                                        <span class="text-sm float-right">
                                        
                                        {mes.date.toDate().toLocaleString('ru', {day: 'numeric', month: 'long', hour: '2-digit', minute: '2-digit'})}</span>

                                        </p>

                                        <!-- Message Text -->
                                        {#if mes.from == user.uid}

                                            <p class="message my">
                                            {mes.text}
                                            </p>
                                        {:else if mes.to == user.displayName}
                                            <p class="message to-me">
                                            {mes.text}
                                            </p>
                                        {:else}
                                            <p class="message def">
                                            {mes.text}
                                            </p>
                                        {/if}
                                    </div>
                                </li>
                                {/if}
                            {/each}
                            </ul>

                        </Collection>

                        <hr>
                    </div>

                    <!-- NEW MESSAGE -->
                    <div class="w-full">
                        
                        <input class="p-1 bg-white" type="text" bind:value={message.text}>
                        
                        <button on:click={() => sendMessage(user)}>Отправить</button>

                    </div>
                </div>

        </User>
    </FirebaseApp>

<style>.message {
  padding: 0.5rem;
  border-radius: 0.25rem;
}

.def {
  background-color: #fff;
}

.my {
  background-color: #fbd38d;
}

.to-me {
  background-color: #9ae6b4;
}

/*# sourceMappingURL=data:application/json;base64, */</style>