<script>
    import { onMount } from 'svelte';
    import {firebase, db} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import ethers from 'ethers';
    import { fade, fly } from 'svelte/transition';
    import tokenABI from './../tokenABI';
    import tokenExABI from './../tokenExABI.js';
    import saveAs from 'file-saver';
    import QRCode from './../Components/QRCode.svelte';
    import QRScanner from './../Components/QRScanner.svelte';

    let etherscanProvider = new ethers.providers.EtherscanProvider();

    let user;
    const TAK = '0x943b8cc219a5fb3ba70adb173774987c63c3080b';
    const exchangeAddress = '0xa3979422e803432b0670ceec8c53c25ed2576a4e';
    let buyTokenBlock;
    let tokenExBalance, tokenRate, tokenRateBN;
    let balanceOfTAK = 0;

    let alertBox, alertBoxContent;
    let cryptoprogress, createWalletProgress, decryprtWalletProgress;
    let authTrue  = false;
    let QRCodeBox  = false;
    let QRScanBox  = false;

    let wallet, defaultWallet, currentWallet, chooseWallet;
    let walletJSON, walletName, walletPassword, walletAddress, walletProvider, walletPrivateKey, walletMnemonic, walletPath;
    let tokenName, tokenAddress, tokenContract, showTokens, addTokenBlock;
    let tokenList = [];
    let walletList = [];
    let selectedWallet;
    let walletHistory;
    let newWalletName, newNwalletPassword, addWalletBlock;

    let provider = ethers.getDefaultProvider();
    let currentNetwork = provider.network.name;

    const TAKContract = new ethers.Contract(TAK, tokenABI, provider);
    const exContract = new ethers.Contract(exchangeAddress, tokenExABI, provider);

    let sendAmount;
    let sendTo;
    let selectedToken;
    let txHash;
    let sendEtherBlock;

    let sendTAKBlock;

    // WALLET INFO
    let viewWalletMnemonic, viewWalletPrivateKey;
    let walletBalance;
    let gasPriceString;

    // IMPORT WALLET
    let importWalletPrivateKey;
    let importedWallet;

    let buyAmount, buyAmountBN, weiAmountBN, weiAmount, ethAmount;

    $: if (buyAmount > 0) {

    let ethwei = ethers.constants.WeiPerEther;

    buyAmountBN = ethers.utils.bigNumberify(buyAmount);

    weiAmountBN = ethwei.mul(buyAmountBN).div(tokenRateBN).add(8);

    weiAmount = weiAmountBN.toHexString();

    ethAmount = ethers.utils.formatEther(weiAmountBN);
    
    }

    // AUTH
    function auth() {
        firebase.auth().onAuthStateChanged(function(usr) {
            if (usr) {
                user = usr;
                getUserWallets();
            } else {
                console.log('no user');
            }
        });
    }

    onMount(auth);

    // CURRENT WALLET
    function setCurrentWallet(w) {

        walletAddress = w.address;
        walletPrivateKey = w.privateKey;
        walletMnemonic = w.mnemonic;

        provider = ethers.getDefaultProvider();
        wallet = new ethers.Wallet(walletPrivateKey, provider);

        getWalletBalance()
        getGasPrice()
        getBalanceOfTAK()
        getAddressTransactionsHistory(wallet.address)

    }

    // Operations
    // ---------------------------------------------------------------------------

    function getWalletBalance() {

        let balancePromise = wallet.getBalance();
    
        balancePromise.then((balance) => {
            let etherString = ethers.utils.formatEther(balance);
            walletBalance = etherString;
        });
    }

    function getWalletTransacionCount() {

        let transactionCountPromise = wallet.getTransactionCount();
    
        transactionCountPromise.then((transactionCount) => {
            console.log(transactionCount);
        });
    }

    function getGasPrice() {

        provider.getGasPrice().then((gasPrice) => {
            // gasPrice is a BigNumber; convert it to a decimal string
            gasPriceString = gasPrice.toString();
    
        });
    }

    // Getting the transaction history of an address
    function getAddressTransactionsHistory(a) {
        let address = a;
        let startBlock = 0;
        let endBlock = "latest";
        etherscanProvider.getHistory(address, startBlock, endBlock)
        .then(function(history) {
            walletHistory = [];
            walletHistory = history.reverse();
        });
    }

    // ALERT BOX
    function alertBoxCall(mes, t) {

        if (!alertBox) {

            alertBoxContent = mes;
            alertBox = true;
        }
        setTimeout(()=>{
            alertBox = false;
        }, t)
    }

    // ----------------------------------------------------------------------------

    // Network change
    function changeEthNetwork(e) {
 
        provider = ethers.getDefaultProvider(e.target.value);
        wallet = new ethers.Wallet(walletPrivateKey, provider);
        getWalletBalance()
        getGasPrice()
        getAddressTransactionsHistory(wallet.address)
    }

    // GET the balance of an account
    function getBalanceOfAnAccount(address) {

        provider.getBalance(address).then((balance) => {

            // balance is a BigNumber (in wei); format is as a sting (in ether)
            let etherString = ethers.utils.formatEther(balance);

            console.log("Balance: " + etherString);
        });
    }

    // Copy private key to clipboard
    function copyPrivateKeyToClipboard() {
        
        let copyText = document.getElementById("private-key");

        copyText.select();
        copyText.setSelectionRange(0, 99999); /*For mobile devices*/

        document.execCommand("copy");

        alertBoxCall('Скопировано.', 2000);
    }

    // copy to clipboard
    function copyToClipboard(id) {
        
        let copyText = document.getElementById(id);

        copyText.select();
        copyText.setSelectionRange(0, 99999); /*For mobile devices*/

        document.execCommand("copy");

        alertBoxCall('Скопировано.', 2000);
    }

    // create Password
    function createWalletPassword() {
        if (walletPassword == undefined || null) {
            alert('Введите пароль!')
        }

        let password = walletPassword;

        function callback(progress) {
            cryptoprogress = parseInt(progress * 100)
            console.log("Encrypting: " + parseInt(progress * 100) + "% complete");
        }


        let encryptPromise = wallet.encrypt(password, callback);

        encryptPromise.then(function(json) {
            walletJSON = json;
            console.log(walletJSON);
            cryptoprogress = null;
        });

    }

    // Download JSON wallet
    function downloadJSONWallet() {

        let curwallet = walletList.filter(el => el.address === wallet.address);
        let data = curwallet[0].wjson;

        let fileName = `${curwallet[0].name}.json`;

        // Create a blob of the data JSON.stringify(data)
        let fileToSave = new Blob([data], {
            type: 'application/json',
            name: fileName
        });

        // Save the file
        saveAs(fileToSave, fileName);
    }

    // Import JSON wallet
    function importWalletJSON() {

        function callback(progress) {
            cryptoprogress = parseInt(progress * 100)
        }

        importedWallet = new ethers.Wallet.fromEncryptedJson(walletJSON, walletPassword, callback);
        setCurrentWallet(importedWallet);
    }

    // Import wallet
    function importWallet() {
        const importWallet = new ethers.Wallet(importWalletPrivateKey)
        setCurrentWallet(importWallet);
        alertBoxCall('Готово', 2000);
    }

    // Add imported wallet
    function addImportedWallet(uid) {
        if (newWalletName == undefined || null) {
            alertBoxCall('Введите название кошелька!', 5000)
        }
        else if (newNwalletPassword == undefined || null) {
            alertBoxCall('Введите пароль для кошелька!', 5000)
        }
        else if (importWalletPrivateKey == undefined || null) {
            alertBoxCall('Введите приватный ключ кошелька!', 5000)
        } 
        
        else {

        const importWallet = new ethers.Wallet(importWalletPrivateKey)

        setCurrentWallet(importWallet);
        walletName = newWalletName;

        const password = walletPassword;

        function callback(progress) {
            createWalletProgress = parseInt(progress * 100);
        }

        const encryptPromise = wallet.encrypt(password, callback);

        encryptPromise.then(function(json) {
            walletJSON = json;
            addWalletToAccount();
            createWalletProgress = null;
        });

        function addWalletToAccount() {
            db.collection(`users/${uid}/wallets`).doc(newWalletName).set({
                name: newWalletName,
                address: walletAddress,
                owner: uid,
                wjson: walletJSON
            })
            .then(function(docRef) {
                addWalletBlock = false;
                alertBoxCall('Кошелёк создан.', 2000);
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
                alertBoxCall(`Ошибка: ${error.message}`, 2000);
            });
        }
        }
    }

    // Get user wallets
    function getUserWallets() {

        db.collection(`users/${user.uid}/wallets`)
        .get().then(function(querySnapshot) {
            querySnapshot.forEach(function(doc) {
                
                const d = doc.data();
                walletList.push({
                    id: walletList.length,
                    name: d.name,
                    address: d.address,
                    wjson: d.wjson
                })
            });
            // console.log(walletList)
        });
    }

    // Open selected wallet
    function openSelectedWallet() {

        const wallet = walletList[selectedWallet];
        walletName = wallet.name;

        function callback(progress) {
            decryprtWalletProgress = parseInt(progress * 100)
        }

        const openWallet = new ethers.Wallet.fromEncryptedJson(wallet.wjson, walletPassword, callback)
        
        openWallet.then((response)=> {
            
            setCurrentWallet(response)
            authTrue = true;
        })
        .catch((err)=> {
            alertBoxCall(`Ошибка: ${err.message}`);
        })
    }

    // Open default wallet
    function openDefaultWallet() {

        function callback(progress) {
            decryprtWalletProgress = parseInt(progress * 100)
        }

        const defaultWallet = new ethers.Wallet.fromEncryptedJson(walletJSON, walletPassword, callback)
        
        defaultWallet.then((response)=> {
            
            setCurrentWallet(response)
            authTrue = true;
        })
        .catch((err)=> {
            alertBoxCall(`Ошибка: ${err.message}`);
        })
    }

    // Load default wallet
    function loadDefaultWallet(uid) {
        db.collection(`users/${uid}/wallets`).doc('default')
        .get()
        .then(function(doc) {
            if (doc.exists) {
                const data = doc.data();
                walletJSON = data.wjson;
                walletName = doc.id;
                
                openDefaultWallet();
            } else {
            
                alert("Ошибка! Кошелек не найден.");
            }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
    }

    // Create default wallet
    function createDefaultWallet(uid) {
        if (walletPassword == undefined || null) {
            alert('Введите пароль!')
        }

        const randomWallet = ethers.Wallet.createRandom();

        setCurrentWallet(randomWallet);

        const password = walletPassword;

        function callback(progress) {
            createWalletProgress = parseInt(progress * 100);
        }

        const encryptPromise = wallet.encrypt(password, callback);

        encryptPromise.then(function(json) {
            walletJSON = json;
            addWalletToAccount();
            createWalletProgress = null;
        });

        function addWalletToAccount() {
            db.collection(`users/${uid}/wallets`).doc('default').set({
                name: 'default',
                address: walletAddress,
                owner: uid,
                wjson: walletJSON
            })
            .then(function(docRef) {
                alertBoxCall('Кошелёк создан.', 2000);
                document.location.reload(true);
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
            });
        }
    }

    // Add custom TOKEN
    function addCustomToken() {

        if (tokenAddress && tokenName == undefined || null) {
            alertBoxCall('Заполните все необходимые поля.', 3000);
        } 

        else {

            db.collection(`users/${user.uid}/wallets/${walletName}/tokens`).add({
                network: provider.network.name,
                name: tokenName,
                address: tokenAddress
            })
            .then(function(docRef) {
                alertBoxCall('Токен добавлен.', 3000);
                addTokenBlock = false;
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
            });
        }

    }

    function getWalletTokens() {

        showTokens = false;
        tokenList.length = 0;

        db.collection(`users/${user.uid}/wallets/${walletName}/tokens`)
        .where("network", "==", provider.network.name)
        .get()
        .then(function(querySnapshot) {
            querySnapshot.forEach(function(doc) {
                
                const data = doc.data();
                tokenList.push({
                    name: data.name,
                    address: data.address,
                    balance: 0,
                    id: doc.id
                });
            });
            getTokensBalance()
        });
    }

    function getTokensBalance() {

        tokenList.forEach(async(tok) => {
            
            const contract = new ethers.Contract(tok.address, tokenABI, provider);
            const balance = await contract.balanceOf(wallet.address);
            tok.balance = await balance.toString();
            showTokens = true;
        });
        console.log('tokenList', tokenList)
    }

    async function getTokenBalance(tokAdrr) {

        const contract = new ethers.Contract(tokAdrr, tokenABI, provider);
        const balance = await contract.balanceOf(wallet.address);
        
        console.log('balance string', balance.toString());
    }

    async function getBalanceOfTAK() {

        const contract = new ethers.Contract(TAK, tokenABI, provider);
        balanceOfTAK = await contract.balanceOf(wallet.address);
        
        // console.log('balanceOfTAK', balanceOfTAK.toString());
    }   

    function createNewWallet(uid) {

        if (newWalletName == undefined || null) {
            alertBoxCall('Введите название кошелька!', 2000)
        }
        else if (newNwalletPassword == undefined || null) {
            alertBoxCall('Введите пароль для кошелька!', 2000)
        }

        else {

            const randomWallet = ethers.Wallet.createRandom();
    
            setCurrentWallet(randomWallet);
    
            const password = walletPassword;
    
            function callback(progress) {
                createWalletProgress = parseInt(progress * 100);
            }
    
            const encryptPromise = wallet.encrypt(password, callback);
    
            encryptPromise.then(function(json) {
                walletJSON = json;
                addWalletToAccount();
                createWalletProgress = null;
            })
            .catch(function(error) {
                alertBoxCall(`Ошибка: ${error.message}`, 5000);;
            });

            function addWalletToAccount() {
                db.collection(`users/${uid}/wallets`).doc(newWalletName).set({
                    name: newWalletName,
                    address: walletAddress,
                    owner: uid,
                    wjson: walletJSON
                })
                .then(function(docRef) {
                    alertBoxCall('Кошелёк создан.', 2000);
                })
                .catch(function(error) {
                    alertBoxCall(`Ошибка: ${error.message}`, 5000);;
                });
            }

        }

    }

    function sendToken() {

        const tokenContract = new ethers.Contract(selectedToken, tokenABI, wallet);

        // How many tokens?
        const numberOfDecimals = 18;
        const numberOfTokens = ethers.utils.parseUnits(sendAmount, numberOfDecimals);

        // Send tokens
        tokenContract.transfer(sendTo, numberOfTokens).then(function(tx) {
            console.log(tx);
            txHash = tx.hash;
            alertBoxCall('Отправлено', 3000)
        })
        .catch((err) => {
            alertBoxCall(`Ошибка: ${err.message}`, 3000)
        })
    }

    function sendTAKToken() {

        const tokenContract = new ethers.Contract(TAK, tokenABI, wallet);

        // How many tokens?
        const numberOfDecimals = 18;
        const numberOfTokens = ethers.utils.parseUnits(sendAmount, numberOfDecimals);

        // Send tokens
        tokenContract.transfer(sendTo, numberOfTokens).then(function(tx) {
            console.log(tx);
            txHash = tx.hash;
            alertBoxCall('Отправлено', 3000)
        })
        .catch((err) => {
            alertBoxCall(`Ошибка: ${err.message}`, 3000)
        })
    }

    function sendEther() {

        let amount = ethers.utils.parseEther(sendAmount);

        let tx = {
            to: sendTo,
            // ... or supports ENS names
            // to: "ricmoo.firefly.eth",

            // We must pass in the amount as wei (1 ether = 1e18 wei), so we
            // use this convenience function to convert ether to wei.
            value: amount
        };

        const sendPromise = wallet.sendTransaction(tx);

        sendPromise.then((tx) => {
            console.log(tx);
            // {
            //    // All transaction fields will be present
            //    "nonce", "gasLimit", "pasPrice", "to", "value", "data",
            //    "from", "hash", "r", "s", "v"
            // }
            alertBoxCall('Отправлено', 3000)
        })
        .catch((err) => {
            alertBoxCall(`Ошибка: ${err.message}`, 3000)
        })
    }

    function buyTakToken() {
        
        const amount = weiAmountBN;

        let tx = {
            to: exchangeAddress,
            // ... or supports ENS names
            // to: "ricmoo.firefly.eth",

            // We must pass in the amount as wei (1 ether = 1e18 wei), so we
            // use this convenience function to convert ether to wei.
            value: amount
        };

        const sendPromise = wallet.sendTransaction(tx);

        sendPromise.then((tx) => {
            console.log(tx);
            // {
            //    // All transaction fields will be present
            //    "nonce", "gasLimit", "pasPrice", "to", "value", "data",
            //    "from", "hash", "r", "s", "v"
            // }
            alertBoxCall('Отправлено', 3000)
        })
        .catch((err) => {
            alertBoxCall(`Ошибка: ${err.message}`, 5000)
        })
    }

    async function getExContractBalance() {
        const response = await TAKContract.balanceOf(exchangeAddress);
        tokenExBalance = ethers.utils.formatEther(response);
    }

    getExContractBalance()

    async function getTokenRate() {
        
        let response = await exContract.getRate();
        tokenRateBN = response;
        tokenRate = response.toString();
    }

    getTokenRate()

</script>

<div class="main-container">
    <FirebaseApp {firebase}>
        <User let:user>
            <Doc path={`users/${user.uid}/wallets/default`} let:data={wallet} let:ref>

                <!-- LOADING -->
                <div slot="loading" class="mt-16 text-white text-center">
                    Загрузка...
                </div>
                
                <!-- NO WALLET -->
                <div slot="fallback">
                    
                    <!-- WELCOME -->
                    <div class="welcome-container p-2 bg-gray-900 h-auto">
                        <div class="welcome-block p-2 bg-gray-100 rounded-lg">

                            <div class="text-justify leading-tight text-sm">

                                <p class=" font-semibold text-center text-base">
                                Добро пожаловать!
                                </p>
                                <p class=" mt-2">
                                    На данный момент у вас нет крипто-кошелька привязанного к аккаунту.
                                    Создайте его прямо сейчас! Придумайте пароль для кошелька и нажмите кнопку "Создать".
                                </p>
                                <p class="mt-1 text-red-600">
                                    <b>Внимание!</b> Кошелёк будет зашифрован с помощью этого пароля, если вы его забудете, то все средства
                                    останутся там навсегда, так как нет другого способа его расшифровать. Рекомендуем записать пароль на бумаге
                                    и хранить в надёжном месте.
                                </p>
                                <p class="mt-1 text-teal-600">
                                    После создания кошельки сохраняются в зашифрованном виде, и каждый раз при открытии необходимо вводить пароль.
                                    Даже если злоумышленники получат доступ к вашему аккаунту, средства на кошельках останутся в сохранности.
                                    Рекомендуем скачать копию кошелька в зашифрованном виде (файл JSON) или приватный ключ кошелька, для доступа к кошельку
                                    из других источников. Кнопки появятся под адресом после создания: "Показать приватный ключ", "Скачать JSON-кошелёк".
                                </p>
                            </div>
                            <div>
                                {#if createWalletProgress != undefined || null}
                                    <progress class="mt-2 w-full rounded-full overflow-hidden" max="100" value={createWalletProgress}></progress>
                                {/if}
                            </div>
                            <div class="mt-3 text-center">
                                
                                <input class="input-password" type="password" placeholder="Пароль" bind:value={walletPassword}>
                                
                            </div>
                            <div class="mt-3 text-center">
                                
                                <button class="btn-orange" on:click|once={()=> createDefaultWallet(user.uid)}> Создать </button>
                                
                            </div>
                        </div>
                    </div>
                </div>

                <!-- WALLET EXIST -->
                <div class="wallet-container px-2 bg-gray-900 h-auto">

                    <!-- Sign Out -->
                    {#if !authTrue}
                    <div class="auth-block p-4 bg-gray-100 rounded-lg">

                        <p class="mb-2 font-semibold text-center text-gray-800">
                            Выберите кошелёк:
                        </p>

                        {#if walletList.length > 1}
                            <div>
                                {#each walletList as w}

                                        <div class="mt-1 p-1 text-orange-600 bg-white border rounded-lg">
                                            
                                            <label for="">
                                                <span>
                                                    {w.name} 
                                                </span>
                                                <input type="radio" bind:group={selectedWallet} value={w.id}>
                                            </label>

                                            <span class=" text-teal-600 text-xs tracking-tight">
                                                {w.address}
                                            </span>
                                        </div>
                                {/each}
                            </div>
                        {:else}
                            <div class="mt-1 p-1 text-orange-600 bg-white border rounded-lg">
                                
                                <label for="">
                                    <span>
                                        {walletList[0].name} 
                                    </span>
                                        <input type="radio" bind:group={selectedWallet} value={0} checked>
                                </label>

                                <span class=" text-teal-600 text-xs tracking-tight">
                                    {walletList[0].address}
                                </span>
                            </div>
                        {/if}

                        <div class="mt-3">
                            <p class=" text-center font-semibold text-gray-700">
                                Введите пароль кошелька:
                            </p>
                        </div>
                        <div>
                            {#if decryprtWalletProgress != undefined || null}
                                <progress class="mt-4 w-full rounded-full overflow-hidden" max="100" value={decryprtWalletProgress}></progress>
                            {/if}
                        </div>
                        <div class="mt-2 text-center">
                            <input class="input-password" type="password" bind:value={walletPassword}>
                        </div>
                        <div class="mt-4 text-center">
                            <button class="btn-orange" on:click={()=> openSelectedWallet()}> Открыть </button>
                        </div>
                    </div>

                    <!-- Sign In -->
                    {:else}
                    <div class="wallet">
        
                        <!-- HEADER -->
                        <div class="wallet-header">
                        </div>

                        <!-- NETWORK -->
                        <div class="wallet-block">

                            <div class=" flex flex-row justify-between items-center">
                                <div>
                                    <span class="text-sm text-gray-700 font-semibold">
                                        Текущая сеть: 
                                    </span>

                                    <!-- help -->
                                    <span class="help">
                                        <img src="./icons/help.svg" alt="help" class="w-4 h-4">
                                        <span>
                                            "Homestead" - главная сеть эфириума (Ethereum mainnet).
                                            "Ropsten" - тестовая сеть эфириума (Ropsten testnet).
                                            "Rinkeby" - тестовая сеть эфириума (Rinkeby mainnet).
                                        </span>
                                    </span>
                                </div>
                                <div>
                                    <span>
                                        <select id="select-network" on:input={changeEthNetwork}>
                                            <option value="homestead">homestead</option>
                                            <option value="rinkeby">rinkeby</option>
                                            <option value="ropsten">ropsten</option>
                                        </select>
                                    </span>
                                </div>
                            </div>
                        </div>

                        <!-- ADD NEW WALLET -->
                        {#if addWalletBlock}
                        <div class="wallet-block">

                            <div class=" flex flex-col">

                                <div>
                                    <p class=" text-sm text-center text-orange-600 font-semibold">
                                        Создать новый кошелёк:
                                    </p>
                                    {#if createWalletProgress != undefined || null}
                                        <progress max="100" class=" mt-2 w-full rounded-full overflow-hidden" value={createWalletProgress}/>
                                    {/if}
                                    <p class="mt-2">
                                        <input class="input-field" type="text" placeholder="Название кошелька" bind:value={newWalletName}>
                                        <button class="btn-gray ml-1" on:click={()=> addWalletBlock = false}>Отмена</button>
                                    </p>
                                    <p class="mt-1">
                                        <input class="input-field" type="text" placeholder="Пароль кошелька" bind:value={newNwalletPassword}>
                                        <button class="btn-orange" on:click={()=> createNewWallet(user.uid)}>Создать</button>
                                    </p>
                                </div>

                                <div class="mt-2">
                                    <p class=" text-sm text-center text-orange-600 font-semibold">
                                        Импортировать существующий кошелёк и сохранить:
                                    </p>
                                    <p>
                                        <input class="input-field" type="text" placeholder="Приватный ключ" bind:value={importWalletPrivateKey}>
                                        <button class="btn-green" on:click={()=> addImportedWallet(user.uid)}>Импорт</button>
                                    </p>
                                </div>

                                <div class="mt-2">
                                    <p class=" text-sm text-center text-orange-600 font-semibold">
                                        Временный импорт:
                                    </p>
                                    <p>
                                        <input class="input-field" type="text" placeholder="Приватный ключ" bind:value={importWalletPrivateKey}>
                                        <button class="btn-orange" on:click={importWallet}>Импорт</button>
                                    </p>
                                </div>
                            </div>
                            
                        </div>
                        {/if}

                        <!-- CURRENT WALLET -->
                        <div class="wallet-block">

                            <div>
                                <div>
                                    <span class="text-sm text-gray-700 font-semibold">
                                        Текущий кошелёк: 
                                    </span>
                                </div>
                                <div class="flex flex-row justify-between items-center">
                                    <!-- Current wallet name -->
                                    <div class=" flex flex-row">
                                        <div class="p-1 w-full border border-gray-400 bg-white rounded-lg">
                                            {walletName}
                                        </div>
                                        <div class="ml-1 p-1 rounded-full bg-teal-500 shadow-md">
                                            <img src="./icons/unfold_more.svg" alt="copy" on:click={()=> authTrue = false} 
                                            class="w-6 h-6 cursor-pointer">
                                        </div>
                                    </div>
                                    <!-- QR-Code generator -->
                                    <div>
                                        <img src="./icons/qr_code.svg" alt="QR" on:click={()=> QRCodeBox = true}
                                        class="w-8 h-8 cursor-pointer">
                                    </div>
                                    <!-- Add wallet -->
                                    <div>
                                        <button class="btn-opt mb-2" on:click={()=> addWalletBlock = true}>
                                        добавить +
                                        </button>
                                    </div>
                                </div>

                                <!-- Address -->
                                <div class="mt-1 flex flex-row justify-between items-center">

                                    <div class=" w-11/12">
                                        <input id="wallet-address" type="text" value={walletAddress}>
                                    </div>

                                    <div class="px-1">
                                        <img src="./icons/file-copy.svg" alt="copy" on:click={()=> copyToClipboard('wallet-address')} class=" w-6 h-6 cursor-pointer">
                                    </div>

                                </div>

                                <div>
                                    <span class="link-wallet-secret" on:click={()=> viewWalletPrivateKey = true}>
                                        Показать приватный ключ
                                    </span>
                                    <span class="link-wallet-secret" on:click={()=> downloadJSONWallet()}>
                                        JSON-кошелёк
                                    </span>
                                </div>
                            </div>

                            <!-- view private key -->
                            {#if viewWalletPrivateKey}
                            <div class="wallet-secret">

                                <div class="mt-2">
                                    
                                    <input id="private-key" class="view-private-key" type="text" value={walletPrivateKey} on:click={copyPrivateKeyToClipboard}>
                                    
                                </div>
                                    
                                <div class="mt-2">
                                    <button class="btn-gray" on:click={()=> viewWalletPrivateKey = false}> Закрыть </button>
                                </div>
                                
                            </div>
                            {/if}

                        </div>

                        <!-- WALLET BALANCE -->
                        <div class="wallet-block">

                            <div class=" flex flex-col">

                                <div>
                                    <p class=" text-center font-semibold text-green-500">
                                        Баланс
                                    </p>
                                </div>

                                <div class="mt-2 flex flex-col">

                                    <!-- ETHEREUM -->
                                    <div class="token">
                                        <div class=" flex flex-row justify-between items-center">

                                            <div class="ml-1 font-semibold text-blue-500">
                                                ETH: 
                                            </div>

                                            <div class=" text-orange-500">
                                                {walletBalance} 
                                            </div>

                                            <div >
                                                {#if !sendEtherBlock}
                                                    <img class="w-8 h-8 cursor-pointer" src="./icons/expand_more.svg" alt="more" 
                                                    on:click={()=> sendEtherBlock = true}>
                                                {:else}
                                                    <img class="w-8 h-8 cursor-pointer" src="./icons/expand_less.svg" alt="less" 
                                                    on:click={()=> sendEtherBlock = false}>
                                                {/if}
                                            </div>

                                        </div>
                                        {#if sendEtherBlock}
                                            <div class="text-center">
                                                <input class="input-field" type="text" placeholder="Адрес получателя" bind:value={sendTo}>
                                                <input class="input-field" type="text" placeholder="Количество" bind:value={sendAmount}>
                                            </div>
                                            <div class="text-center">
                                                <button class="btn-white" on:click={()=> QRScanBox = true}>
                                                <img src="./icons/scan.svg" alt="QR" class="w-6 h-6">
                                                </button>
                                                <button class="btn-green" on:click={sendEther}> Отправить </button>
                                            </div>
                                        {/if}
                                    </div>
                                    
                                    <!-- TAK -->  
                                    <div class="token">
                                        <div class=" flex flex-row justify-between items-center">

                                            <div class="ml-1 font-semibold text-blue-500">
                                                TAK: 
                                            </div>

                                            <div class=" text-orange-500">
                                                {balanceOfTAK}
                                            </div>

                                            <div >
                                                {#if !sendTAKBlock}
                                                    <img class="w-8 h-8 cursor-pointer" src="./icons/expand_more.svg" alt="more" 
                                                    on:click={()=> sendTAKBlock = true}>
                                                {:else}
                                                    <img class="w-8 h-8 cursor-pointer" src="./icons/expand_less.svg" alt="less" 
                                                    on:click={()=> sendTAKBlock = false}>
                                                {/if}
                                            </div>

                                        </div>
                                        {#if sendTAKBlock}
                                            <div class="text-center">
                                                <input class="input-field" type="text" placeholder="Адрес получателя" bind:value={sendTo}>
                                                <input class="input-field" type="text" placeholder="Количество" bind:value={sendAmount}>
                                            </div>
                                            <div class="text-center">
                                                <button class="btn-white" on:click={()=> QRScanBox = true}>
                                                <img src="./icons/scan.svg" alt="QR" class="w-6 h-6">
                                                </button>
                                                <button class="btn-green" on:click={sendTAKToken}> Отправить </button>
                                            </div>
                                        {/if}
                                    </div>

                                    <!-- Buy TAK token -->
                                    <div class=" text-center">
                                        <p on:click={()=> buyTokenBlock = true}
                                            class="text-sm font-medium underline text-teal-500">
                                            Купить токены ТАК
                                        </p>

                                        {#if buyTokenBlock}
                                        <div class="mt-2">
                                            <div>
                                                <input class=" border bg-gray-200"
                                                type="number" step="1000" min="1000" max={tokenExBalance} bind:value={buyAmount}>
                                            </div>
                                            <div class=" text-center">
                                                <span class=" text-sm text-teal-500">
                                                    = {ethAmount} Eth.
                                                </span>
                                            </div>
                                            <div>
                                                <button class="btn-green" on:click={buyTakToken}>Купить</button>
                                            </div>
                                        </div>
                                        {/if}
                                    </div>

                                    <!-- GAS PRICE -->
                                    <!-- <div class="token">
                                        <p>
                                            <span class="ml-1 font-semibold text-blue-500">
                                                Цена газа: 
                                            </span>

                                            <span class="ml-10 text-orange-500">
                                                {Math.trunc(+gasPriceString / 1000000000)} gwei 
                                            </span>
                                        </p>
                                    </div> -->
                                </div>

                                <div class="mt-2 text-center">
                                    <button class="btn-opt" on:click={getWalletTokens}>Показать токены</button>
                                    <button class="btn-opt" on:click={()=> addTokenBlock = true}>Добавить токен</button>
                                </div>
                            </div>

                            <!-- TOKENS -->
                            {#if showTokens}
                                <div class="mt-3 flex flex-col">
                                    {#each tokenList as token}
                                        
                                        <div class="token">
                                            <div class=" flex flex-row justify-between items-center">

                                                <div>
                                                    <span class=" text-blue-500">
                                                        {token.name}:
                                                    </span>
                                                </div>

                                                <div>
                                                    <span class=" text-orange-500">
                                                        {+token.balance / 1e18}
                                                    </span>
                                                </div>
                                                <div>
                                                    <label>
                                                        <input type="radio" bind:group={selectedToken} value={token.address}>
                                                    </label>
                                                </div>
                                            </div>
                                        </div>
                                    {/each}
                                </div>
                            {/if}
                        </div>

                        <!-- ADD TOKEN -->
                        {#if addTokenBlock}
                        <div class="wallet-block">
                            <div>
                                <p class=" text-center font-semibold text-teal-500">
                                    Добавить токен:
                                </p>
                            </div>
                            <div class=" text-center">
                                <input class="input-field" type="text" placeholder="Название токена" bind:value={tokenName}>
                                <input class="input-field" type="text" placeholder="Адрес смарт-контракта токена" bind:value={tokenAddress}>
                            </div>
                            <div class=" text-center">
                                <button class="btn-orange" on:click={addCustomToken}> Добавить </button>
                                <button class="btn-gray" on:click={()=> addTokenBlock = false}> Отмена </button>
                            </div>
                        </div>
                        {/if}

                        <!-- TRANSFER TOKEN-->
                        {#if selectedToken}
                        <div class="wallet-block">
                            <div>
                                <p class=" text-center font-semibold text-teal-500">
                                    Отправить токены
                                </p>
                                
                                <div class=" text-center">
                                    <input class="input-field" type="text" placeholder="Адрес получателя" bind:value={sendTo}>
                                    <input class="input-field" type="text" placeholder="Количество" bind:value={sendAmount}>
                                </div>
                                <div class=" text-center">
                                    <button class="btn-white" on:click={()=> QRScanBox = true}>
                                        <img src="./icons/scan.svg" alt="QR" class="w-6 h-6">
                                    </button>
                                    <button class="btn-green" on:click={sendToken}> Отправить </button>
                                </div>
                                {#if txHash}
                                    <div class="mt-1 p-1 w-full bg-white rounded-lg truncate">
                                        <a class="link-hash" href={`https://${provider.network.name}.etherscan.io/tx/${txHash}`}> 
                                        {txHash} 
                                        </a>
                                    </div>
                                {/if}
                            </div>
                        </div>
                        {/if}

                        <!-- WALLET HISTORY -->
                        <div class="wallet-block">
                            <p class=" font-semibold text-teal-500 text-center">
                                Транзакции
                            </p>
                            {#if walletHistory && walletHistory.length > 0}
                            
                                {#each walletHistory as h}
                                    <div class="mt-1 p-1 bg-white border rounded-lg text-sm">

                                        <div class="px-1 flex flex-row justify-between text-gray-600">
                                            <div>
                                                {new Date(h.timestamp * 1000).getHours()}:{new Date(h.timestamp * 1000).getMinutes()}
                                            </div>
                                            <div>
                                                {new Date(h.timestamp * 1000).toLocaleDateString('ru-RU', {day: 'numeric', month: 'long', year: 'numeric'})}
                                            </div>
                                        </div>

                                        <div class="mt-2 text-center">
                                            <p>
                                                <span class=" text-base font-medium text-orange-500">
                                                    { ethers.utils.formatEther(h.value.toString()) } eth.
                                                </span>
                                            </p>
                                        </div>

                                        <div class="mt-2 px-1 truncate">
                                            {#if provider.network.name === 'homestead'}
                                                <a class="link-hash" href={`https://etherscan.io/tx/${h.hash}`} target="_blank"> 
                                                    {h.hash} 
                                                </a>
                                            {:else}
                                                <a class="link-hash" href={`https://${provider.network.name}.etherscan.io/tx/${h.hash}`} target="_blank"> 
                                                    {h.hash} 
                                                </a>
                                            {/if}
                                        </div>
                                        
                                    </div>
                                {/each}
                            {:else}
                                <p class="mt-4 p-2 text-sm italic text-center">
                                    Нет транзакций
                                </p>
                            {/if}
                        </div>

                    </div>
                    {/if}
                </div>
                
            </Doc>
        </User>
    </FirebaseApp>
</div>

<!-- ALERT BOX -->
{#if alertBox}
    <div id="alertBox" class=" fixed top-0">

        <div transition:fade="{{ duration: 500 }}" class="p-3 border bg-white rounded-lg shadow-lg">

            <p class="text-center">
                {alertBoxContent}
            </p>

            <p class="mt-2 text-center">
                <button class="btn-orange" on:click={() => alertBox = false}>Ок</button>
            </p>

        </div>

    </div>
{/if}

<!-- QRCode -->
{#if QRCodeBox}
    <div class=" fixed top-0">
        <QRCode codeValue={walletAddress} squareSize="250" on:close={()=> QRCodeBox = false}/>
    </div>
{/if}

<!-- QRCode Scanner -->
{#if QRScanBox}
    <div class=" fixed top-0 w-full">
        <QRScanner on:result={(e)=> sendTo = e.detail.result} on:close={()=> QRScanBox = false}/>
    </div>
{/if}


<style>.help {
