  width: 16rem;
  height: auto;
  font-size: 0.75rem;
  background-color: #2d3748;
  color: #f7fafc;
  border-radius: 0.5rem;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}

.help:hover span {
  position: absolute;
  display: -webkit-inline-box;
  display: inline-flex;
  margin-top: 0.5rem;
  margin-left: -5rem;
}

