<script>
    import ethers from 'ethers';
    import tokenExABI from './../tokenExABI.js';
    import tokenABI from './../tokenABI.js';

    let provider;
    let signer;
    let progressVal = 0;
    let buyAmount;
    let buyAmountBN;
    let weiAmount;
    let weiAmountBN;
    let ethAmount = 0;
    let ethAmountBN;
    let tokenExBalance;
    let newRate;
    let curRate;
    let tokenRate;
    let tokenRateBN;
    let USD = 0;
    let ETH = 0;
    let isMetamsk = false;
    let userWalletAddress;
    let userTokenBalance;
    let ethwei = ethers.constants.WeiPerEther;

    const tokenAddress = '0x943B8cC219a5Fb3BA70AdB173774987c63c3080b';
    const exchangeAddress = '0xa3979422e803432b0670ceec8c53c25ed2576a4e';

    if (window.web3) {
        
        provider = new ethers.providers.Web3Provider(web3.currentProvider);
        signer = provider.getSigner();

    } else {

        provider = ethers.getDefaultProvider();
    }



    let exContract = new ethers.Contract(exchangeAddress, tokenExABI, provider);
    let tokenContract = new ethers.Contract(tokenAddress, tokenABI, provider);


    $: if (buyAmount > 0) {

        buyAmountBN = ethers.utils.bigNumberify(buyAmount);

        weiAmountBN = ethwei.mul(buyAmountBN).div(tokenRateBN).add(8);

        weiAmount = weiAmountBN.toHexString();

        ethAmount = ethers.utils.formatEther(weiAmountBN);
        
    }


    async function getTokenRate() {
        
        let response = await exContract.getRate();
        tokenRateBN = response;
        tokenRate = response.toString();
    }

    getTokenRate()

    async function getUserTokenBalance() {

        let response = await tokenContract.balanceOf(userWalletAddress);
        userTokenBalance = ethers.utils.formatEther(response);
    }

    async function getExContractBalance() {

        let response = await tokenContract.balanceOf(exchangeAddress);
        tokenExBalance = ethers.utils.formatEther(response);
        progressVal = 5000000 - tokenExBalance;
    }

    getExContractBalance()

    function checkMetamask() {

        if (typeof window.ethereum !== 'undefined') {

            if (ethereum.isMetaMask) {

                isMetamsk = true;
            }
        } else {

            isMetamsk = false;
        }
    }

    checkMetamask();
    
    function connectMetamask() {
        window.ethereum.enable()
        userWalletAddress = ethereum.selectedAddress;
        getUserTokenBalance();
    }

    function getCurrencyRate() {

        fetch('https://www.cbr-xml-daily.ru/daily_json.js')
        .then((response) => {
            return response.json();
        })
        .then((data) => {
            curRate = data;
            USD = curRate.Valute.USD.Value;
        });

    }

    getCurrencyRate()

    function getEthPrice() {

        let etherscanProvider = new ethers.providers.EtherscanProvider();

        // Getting the current Ethereum price
        etherscanProvider.getEtherPrice().then(function(price) {
            ETH = price;
        });
    }

    getEthPrice()

    function addTokenToMetamask() {

        ethereum.sendAsync(
        {
            method: 'wallet_watchAsset',
            params: {
            type: 'ERC20',
            options: {
                address: tokenAddress,
                symbol: 'T777',
                decimals: 18,
            },
            },
            id: Math.round(Math.random() * 100000),
        },
        (err, added) => {
            if (added) {
            console.log('Thanks for your interest!');
            } else {
            console.log('Your loss!');
            }
        }
        );
    }

    function buyTokens() {

        if (tokenExBalance > buyAmount) {

            let amount = weiAmount;

            console.log(amount)

            const transactionParameters = {
            // nonce: '0x00', // ignored by MetaMask
            // gasPrice: '0x09184e72a000', // customizable by user during MetaMask confirmation.
            // gas: '0x2710', // customizable by user during MetaMask confirmation.
            to: exchangeAddress, // Required except during contract publications.
            from: ethereum.selectedAddress, // must match user's active address.
            value: amount, // Only required to send ether to the recipient from the initiating external account.
            // data: '0x7f7465737432000000000000000000000000000000000000000000000000000000600057', // Optional, but used for defining smart contract creation and interaction.
            // chainId: 3, // Used to prevent transaction reuse across blockchains. Auto-filled by MetaMask.
            };

            ethereum.sendAsync(
            {
                method: 'eth_sendTransaction',
                params: [transactionParameters],
                from: ethereum.selectedAddress,
            },
            (err, result) => {
                if (err) console.error(err);
                else console.log(result);
            }
            );
        } else {
            alert(`Доступное кол-во токенов для покупки: ${tokenExBalance}`)
        }
    }

    async function setRate() {

        let contractWithSigner = exContract.connect(signer);

        let tx = await contractWithSigner.changeRate(newRate);

        console.log(tx.hash);

        await tx.wait();;

        console.log('done');
    }

    async function withdraw() {

        let contractWithSigner = exContract.connect(signer);

        let tx = await contractWithSigner.withdraw();

        console.log(tx.hash);

        await tx.wait();;

        console.log('done');
    }


</script>

<div class="pt-16 min-h-screen bg-gradient-purple-pink-light">

    <!-- HEADER -->
    <div class="p-1 text-sm font-medium">
        <div>
            USD: 
            {Math.trunc(USD * 100) / 100} руб.

            <!-- help -->
            <span class="help">
                <img src="./icons/help.svg" alt="help" class="w-4 h-4">
                <span>
                    Курс долара США от ЦБ РФ.
                </span>
            </span>
        </div>
        <div>
            ETH: 
            {ETH} USD
            <!-- help -->
            <span class="help">
                <img src="./icons/help.svg" alt="help" class="w-4 h-4">
                <span>
                    Курс эфириума от 
                    <a href="https://etherscan.io" class=" underline text-orange-500"> 
                    Etherscan 
                    </a>
                </span>
            </span>
        </div>
        <div>
            ETH/RUB: 
            {Math.trunc((ETH * USD) * 100) / 100} руб.
        </div>
        <div>
            ETH/ТАК: {tokenRate} TAK
            <!-- help -->
            <span class="help">
                <img src="./icons/help.svg" alt="help" class="w-4 h-4">
                <span>
                    Токен Avtokarta.club, цена указана со скидкой 50%
                </span>
            </span>
        </div>
    </div>

    <!-- PROGRESS -->
    <div>
        <div>
            <progress value={progressVal} max="5000000" class="mt-2 w-full h-8">     
            </progress>
        </div>
        {#if tokenExBalance != undefined}
        <div class=" text-xs italic text-center text-purple-900">
            <span>
                Продано: {Math.trunc(5000000 - tokenExBalance)}
            </span>
            /
            <span>
                Доступно: {Math.trunc(tokenExBalance)}
            </span>
        </div>
        {/if}
    </div>

    <!-- BUY TOKEN -->
    {#if isMetamsk}
    <div class="mt-3 p-2">

        <!-- Info -->
        {#if userWalletAddress != undefined}
        <div class=" text-sm">
            <div class=" font-medium">
                Ваш кошелёк: 
            </div>
            <div>
                {userWalletAddress}
            </div>
            <div>
                <span class=" font-medium">
                    Кол-во ТАК: 
                </span>
                {Math.trunc(userTokenBalance)}
            </div>
        </div>
        {/if}

        <div class="mt-2 text-center">
            <button class="btn-red-pink" on:click={connectMetamask}>Подключить MetaMask</button>
            <button class="btn-purple-pink" on:click={addTokenToMetamask}>Добавить токены в кошелёк</button>
        </div>

        <!-- Buy tokens -->
        <div class="mt-4">
            <div class=" text-lg font-semibold text-center text-white">
                
                Купить токены
                
            </div>

            <div class="mt-2 text-center">
                <input class=" border bg-gray-200"
                type="number" step="1000" min="1000" max={tokenExBalance} bind:value={buyAmount}>
            </div>
            
            <div class=" text-center">
                <span class=" text-sm text-white">
                    = {ethAmount} Eth.
                </span>
            </div>

            <div class="mt-2 text-center">
                <button class="btn-blue-green" on:click={buyTokens}>Купить</button>
            </div>
        </div>

        <!-- <div class="mt-10 text-center">
            <input type="text" bind:value={newRate}>
            <button class="btn-orange" on:click={setRate}>set rate</button>
            <button class="btn-green" on:click={withdraw}>withdraw</button>
        </div> -->

    </div>
    {:else}
    <div class="mt-6 p-2">

        <p class="p-2 mx-auto border bg-white rounded-lg shadow-lg">
            Для покупки токенов установите расширение для браузера 
            <a class=" underline text-teal-500 font-semibold"
            href="https://chrome.google.com/webstore/detail/metamask/nkbihfbeogaeaoehlefnkodbefgpgknn">
            MetaMask
            </a>
        </p>

    </div>
    {/if}
</div>

<style>.help {
  position: relative;
  display: inline-block;
}

.help span {
  display: none;
  z-index: 10;
  padding: 0.5rem;
  width: 16rem;
  height: auto;
  font-size: 0.75rem;
  background-color: #2d3748;
  color: #f7fafc;
  border-radius: 0.5rem;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}

.help:hover span {
  position: absolute;
  display: -webkit-inline-box;
  display: inline-flex;
  margin-top: 0.5rem;
  margin-left: -5rem;
}

