<script>
    import { fade, fly, scale } from 'svelte/transition';
    import {firebase, db, storage} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection, collectionStore} from 'sveltefire';

    export let params = {};

    let storageRef = storage.ref();

    // AUTH USER and DB
    let getUser;
    let getUserAccount;

    firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
            // User is signed in.
            getUser = user;
        } else {
            // No user is signed in.
            console.log('no user login')
        }
    });

    let alertBox, alertBoxContent;
    // ALERT BOX
    function alertBoxCall(mes) {

        if (!alertBox) {

            alertBoxContent = mes;
            alertBox = true;
        }
        setTimeout(()=>{
            alertBox = false;
        }, 5000)
    }

    let serviceRating;
    let rating = 0;
    let reviewText = '';
    let options, addBlock, imgURL;
    let title = 'Заголовок';
    let desc = 'Описание';

    let queryBlocks = (ref) => ref.orderBy("desc").limit(1)
 
    function nextBlock(last) {
        queryBlocks = (ref) => ref.orderBy("desc").startAfter(last.desc).limit(1);
    }

    let queryReviews = (ref) => ref.orderBy("date", "desc").limit(3)
 
    function nextReviews(last) {
        queryReviews = (ref) => ref.orderBy("date", "desc").startAfter(last.date).limit(3);
    }
    
    // get service rating
    function getRating() {

        let ratings = [];

        let reviews = db.collection(`services/${params.id}/reviews`).get()
        .then(function(querySnapshot) {
            querySnapshot.forEach(function(doc) {

                let data = doc.data();
                ratings.push(data.rating)

            });
            let sum = ratings.reduce((a, b)=> a + b, 0);
            let ave = sum / ratings.length;
            serviceRating = Math.trunc(ave * 10) / 10;
        });

    }

    getRating()

    // Upload block foto
    function blockFotoUpload(e, uid) {
        e.stopPropagation();
        e.preventDefault();
        
        let file = e.target.files[0];

        let metadata = {
            'contentType': file.type
        };

        storageRef.child(`users/${uid}/service/${params.id}/${file.name}`)
        .put(file, metadata)
        .then(function(snapshot) {
            // console.log('Uploaded', snapshot.totalBytes, 'bytes.');
            // console.log('File metadata:', snapshot.metadata);
            // Let's get a download URL for the file.
            snapshot.ref.getDownloadURL().then(function(url) {
                console.log('File available at', url);
                imgURL = url;
            });
        })
        .catch(function(error) {
            // [START onfailure]
            console.error('Upload failed:', error);
            // [END onfailure]
        });
    }

    function addNewBlock() {
        if (imgURL != undefined) {

            db.collection(`services/${params.id}/adblocks`)
            .add({
                imgURL: imgURL,
                title: title,
                desc: desc,
                owner: getUser.uid
            })
            .then(function() {
                alertBoxCall('Рекламный блок добавлен! Обновите страницу.');
                options = false;
                addBlock = false; 
                imgURL = null;
                title = 'Заголовок';
                desc = 'Описание';
            })
            .catch(function(error) {
                // The document probably doesn't exist.
                console.error("Error updating document: ", error);
            });
        }
    }

    function addReview() {
        if (reviewText.length > 15) {

            db.collection(`services/${params.id}/reviews`).where("owner", "==", getUser.uid).get()
            .then(function(querySnapshot) {
                    
                if (querySnapshot.empty) {
                    add();
                } else {
                    alertBoxCall('Отзыв можно оставить после оплаты услуг сервиса, но только один отзыв разрешается написать без оплаты.')
                }
            
            })
            .catch(function(error) {
                console.log("Error getting documents: ", error);
            });

            function add() {

                db.collection(`services/${params.id}/reviews`)
                .add({
                    date: firebase.firestore.FieldValue.serverTimestamp(),
                    text: reviewText,
                    rating: rating,
                    owner: getUser.uid,
                    username: getUser.displayName
                })
                .then(function() {
                    rating = 0;
                    reviewText = '';
                    alertBoxCall('Спасибо, отзыв добавлен!');
                })
                .catch(function(error) {
                    // The document probably doesn't exist.
                    console.error("Error updating document: ", error);
                });
            }
        } else {

            alertBoxCall('Слишком короткий отзыв!');
        }
    }
    
    let replyText = '';
    let reviewID;
    let replyReviewBox;

    function replyReview(id) {
        reviewID = id;
        replyReviewBox = true;
    }

    function replySend() {
        if (replyText.length > 15) {

            db.collection(`services/${params.id}/reviews/${reviewID}/reply`)
            .add({
                date: firebase.firestore.FieldValue.serverTimestamp(),
                text: replyText,
                owner: getUser.uid,
                username: getUser.displayName
            })
            .then(function() {
                replyReviewBox = false;
                replyText = '';
                alertBoxCall('Спасибо, ответ добавлен!');
            })
            .catch(function(error) {
                // The document probably doesn't exist.
                console.error("Error updating document: ", error);
            });
        } else {

            alertBoxCall('Слишком короткий ответ!');
        }
    }

    let recTime;
    let changeTime;
    let recDate = params.date? params.date : null;
    let recordsBox = params.records? true : false;
    let receptionBox;

    let settings;
    let subService;
    let newSubService;

    let logSub, logDate, logTime, logDesc;
   
    function addNewRecord() {

        if(logSub && logDate && logTime && logDesc != undefined) {

            db.collection(`services/${params.id}/records`).add({
                clientname: getUser.displayName,
                clientUID: getUser.uid,
                subservice: logSub,
                date: logDate,
                time: logTime,
                desc: logDesc
            })
            .then(function(docRef) {
                receptionBox = false;
                alertBoxCall('Вы записаны, ждите ответа мастера.');
            })
            .catch(function(error) {
                alertBoxCall('Ошибка.');
            });
        } else {
            alertBoxCall('Заполните все поля.')
        }

    }

    let notificationPermisson = Notification.permission;
    if (notificationPermisson === "granted") {
        listenReception()
    }

    function checkNotification() {
        Notification.requestPermission().then(function(result) {
            notificationPermisson = result;
            if (notificationPermisson === "granted") {
                listenReception()
            }
        });
    }

    function createNotification(clientname, subservice, date, serviceId) {
        var img = './icons/main-logo.png';
        var text = `${clientname} записался на ${subservice}`;
        var notification = new Notification('К вам записались, клиент ждёт подтверждения.', { body: text, icon: img })
        .onclick = (e) => {
            recDate = date;
            recordsBox = true;
            e.preventDefault();
            window.open(`https://avtokarta.club/#/service/${serviceId}/true/${date}`, '_blank');
        }
    }
    
    function listenReception() {
        db.collection(`services/${params.id}/records`)
        .onSnapshot(function(snapshot) {
            snapshot.docChanges().forEach(function(change) {
                if (change.type === "added") {
                    let record = change.doc.data();
                    if (!record.confirmed) {

                        createNotification(record.clientname, record.subservice, record.date, params.id)
                    }
                }
                // if (change.type === "modified") {
                //     console.log("Modified city: ", change.doc.data());
                // }
                // if (change.type === "removed") {
                //     console.log("Removed city: ", change.doc.data());
                // }
            });
        });
    }

</script>

<div class="mt-16">
<FirebaseApp {firebase}>
    <User let:user>
        <Doc path={`services/${params.id}`} let:data={service} let:ref={serviceRef}>

            <!-- HEADER -->
            <div class="p-2 rounded-lg shadow-lg">

                <!-- header -->
                <div class="p-1 flex flex-row">
                    <!-- rating -->
                    <div class="p-2 flex flex-col content-center items-center">
                        <div>
                            <img src="./icons/star.svg" alt="star" class="w-8 h-8">
                        </div>
                        <div class="mt-1 font-semibold text-center">
                            {#if serviceRating < 3}
                            <span class=" text-red-600">
                                {serviceRating}
                            </span>
                            {/if}
                            {#if serviceRating >= 3 && serviceRating < 4}
                            <span class=" text-orange-600">
                                {serviceRating}
                            </span>
                            {/if}
                            {#if serviceRating >= 4}
                            <span class=" text-green-600">
                                {serviceRating}
                            </span>
                            {/if}
                        </div>
                    </div>
                    <!-- service name -->
                    <div class="flex flex-grow">
                        <div class="p-2 my-auto mx-auto text-center">
                            <p class="font-semibold text-lg">
                                {service.name}
                            </p>
                        </div>
                    </div>
                </div>

                <!-- service offer -->
                <div>
                    <p class="p-2 text-sm text-justify">
                        {service.offer}
                    </p>
                </div>
            
                <!-- if owner -->
                {#if service.owner == getUser.uid}
                    <div>
                        <img src="./icons/expand_more.svg" alt="options" 
                        class=" mx-auto"
                        on:click={()=> options = options ? false : true}>
                    </div>
                {/if}

                <!-- OPTIONS -->
                {#if options}
                <div>
                    <div class="option-menu" on:click={()=> recordsBox = recordsBox ? false : true}>
                        Журнал
                    </div>
                    <div class="option-menu" on:click={()=> settings = settings ? false : true}>
                        Настройки
                    </div>
                    {#if settings}
                        <div class="mt-1 p-1 border border-dashed">
                            <div>
                                <span class=" text-sm">
                                    Предварительная запись через сайт:
                                </span>
                                {#if service.subServices}
                                    {#if service.reception}
                                    <span class=" text-sm text-green-600">Включено</span>
                                    <button class="btn-gray" on:click={()=> serviceRef.update({reception: false})}>отключить</button>
                                    {:else}
                                    <span class=" text-sm text-gray-500">Выключено</span>
                                    <button class="btn-green" on:click={()=> serviceRef.update({reception: true})}>включить</button>
                                    {/if}
                                {:else}
                                    <span class=" text-sm text-orange-600 italic">
                                        Сначала добавьте услугу...
                                    </span>
                                {/if}
                            </div>
                            <div>
                                <span class=" text-sm">
                                    Уведомления:
                                </span>
                                {#if notificationPermisson == "granted"}
                                <span class=" text-sm text-green-600">Включено</span>
                                {:else}
                                <span class=" text-sm text-gray-500">Выключено</span>
                                <button class="btn-green" on:click={()=> checkNotification()}>включить</button>
                                {/if}
                            </div>
                        </div>
                    {/if}

                    <div class="option-menu" on:click={()=> subService = subService ? false : true}>
                        Добавить услугу
                    </div>
                    {#if subService}
                        <div class="pt-1 p-1 border border-dashed">
                            <input type="text" placeholder="Название услуги" bind:value={newSubService}>
                            <button class="btn-orange" 
                            on:click={()=> serviceRef.update({subServices: firebase.firestore.FieldValue.arrayUnion(newSubService)})
                            .then(()=> alertBoxCall('Услуга добавлена.'))}>
                            добавить</button>
                        </div>
                    {/if}

                    <div class="option-menu" on:click={()=> addBlock = addBlock ? false : true}>
                        Добавить блок
                    </div>
                    {#if addBlock}
                        <div>
                            <div class="image-upload">
                                {#if imgURL != undefined}

                                    <img class="img-up" src={imgURL} alt="block-img"/>

                                {:else}
                                    <label for="block-foto">
                                                
                                        <p class="my-4">Загрузите изображение</p>
                                        <img src="./icons/add_photo_teal.svg" alt="add_photo" class="img-icon">
                                    </label>
                                    <input type="file" id="block-foto" name="file" on:change={(e) => blockFotoUpload(e, user.uid)}/>
                                    
                                {/if}
                            </div>

                            <div class="mt-2 text-center">

                                <input type="text" bind:value={title} maxlength="50">
                                <input type="text" bind:value={desc} maxlength="150">

                                {#if imgURL != undefined}
                                    <button class="btn-green" on:click={addNewBlock}>Добавить</button>
                                {/if}
                            </div>
                        </div>
                    {/if}
                </div>
                {/if}
            </div>

            <!-- AD BLOCKS -->
            <div class="p-1">    
                <Collection path={serviceRef.collection('adblocks')} query={queryBlocks} let:data={blocks} let:last>
                    {#each blocks as block}
                    <div class=" relative border rounded shadow-md" on:click={() => nextBlock(last)}>

                        <span class="absolute p-1 mt-3 bg-gray-900 text-white font-semibold uppercase opacity-75">
                            {block.title}
                        </span>

                        <span class="absolute right-0 m-2 opacity-50">
                            <img src="./icons/close_circle.svg" alt="X" class="w-8 h-8">
                        </span>

                        <img src="{block.imgURL}" alt="block-img" class="img-up">

                        <hr>

                        <div class="p-2 bg-gray-100 text-black text-sm leading-tight">

                            {block.desc}

                            {#if service.owner == getUser.uid}
                                <div class="mt-1 text-center">
                                    <button class="btn-opt" on:click={()=> block.ref.delete()}>Удалить блок</button>
                                </div>
                            {/if}
                        </div>
                    </div>
                    {/each}
                </Collection>    
            </div>

            <!-- RATING -->
            <div class="rating-container">
                <div class="star-container">
                    {#if rating >= 1}
                        <img src="./icons/star.svg" alt="star" class="star" on:click={()=> rating = 1}>
                    {:else}
                        <img src="./icons/star_border.svg" alt="star" class="star" on:click={()=> rating = 1}>
                    {/if}
                    {#if rating >= 2}
                        <img src="./icons/star.svg" alt="star" class="star" on:click={()=> rating = 2}>
                    {:else}
                        <img src="./icons/star_border.svg" alt="star" class="star" on:click={()=> rating = 2}>
                    {/if}
                    {#if rating >= 3}
                        <img src="./icons/star.svg" alt="star" class="star" on:click={()=> rating = 3}>
                    {:else}
                        <img src="./icons/star_border.svg" alt="star" class="star" on:click={()=> rating = 3}>
                    {/if}
                    {#if rating >= 4}
                        <img src="./icons/star.svg" alt="star" class="star" on:click={()=> rating = 4}>
                    {:else}
                        <img src="./icons/star_border.svg" alt="star" class="star" on:click={()=> rating = 4}>
                    {/if}
                    {#if rating == 5}
                        <img src="./icons/star.svg" alt="star" class="star" on:click={()=> rating = 5}>
                    {:else}
                        <img src="./icons/star_border.svg" alt="star" class="star" on:click={()=> rating = 5}>
                    {/if}
                </div>

                <div class="mt-1 inline-block">
                    <input type="text" placeholder="Ваш отзыв" bind:value={reviewText}>
                    <button class="btn-orange" on:click={addReview}>Добавить</button>
                </div>
            </div>

            <!-- REVIEWS -->
            <div class="p-1">
                <Collection path={serviceRef.collection('reviews')} query={queryReviews} let:data={reviews} let:ref={reviewsRef} let:last>

                    <div class="mt-1">
                        {#each reviews as review}
                        <div class="review">
                            <p>
                                <span class=" font-semibold">
                                    {review.username} 
                                </span>

                                <!-- stars -->
                                <span class=" float-right">
                                {#if review.rating >= 1}
                                    <img src="./icons/star.svg" alt="star" class="star-small">
                                    {#if review.rating >= 2}
                                        <img src="./icons/star.svg" alt="star" class="star-small">
                                        {#if review.rating >= 3}
                                            <img src="./icons/star.svg" alt="star" class="star-small">
                                            {#if review.rating >= 4}
                                                <img src="./icons/star.svg" alt="star" class="star-small">
                                                {#if review.rating == 5}
                                                    <img src="./icons/star.svg" alt="star" class="star-small">
                                                {/if}
                                            {/if}
                                        {/if}
                                    {/if}
                                {/if}
                                </span>
                            </p>
                            <p class="mt-2">
                                {review.text}
                                {#if service.owner == getUser.uid}
                                    <span class=" text-teal-500 text-xs" on:click={()=> replyReview(review.id)}>
                                        Ответить
                                    </span>
                                {/if}
                            </p>

                            <!-- REPLY -->
                            <Collection path={review.ref.collection('reply')} let:data={reply} let:ref={replyRef}>
                                {#if reply.length >= 1}
                                    {#each reply as r}
                                        <div class="reply">
                                            <p class=" font-semibold">
                                                {r.username}
                                            </p>
                                            <p class="mt-1">
                                                {r.text}
                                            </p>
                                        </div>
                                    {/each}
                                {/if}
                            </Collection>

                        </div>
                        {/each}
                    </div>
                    
                    <!-- MORE reviews -->
                    <div>
                        <img src="./icons/expand_more.svg" alt="options" 
                        class=" mx-auto"
                        on:click={()=> nextReviews(last)}>
                    </div>
                    
                    <!-- NO reviews -->
                    {#if reviews.length == 0}
                    <div class="mt-1 p-2 bg-gray-100 border rounded-md text-center">
                        <p>Отзывов нет</p>
                    </div>
                    {/if}

                    <span slot="fallback">Не удалось загрузить</span>
                </Collection>
            </div>

            <!-- RECEPTION BUTTON-->
            {#if service.reception}
                <div transition:scale="{{ duration: 1000 }}" class="absolute bottom-0 left-0 w-20 h-20">
                    <div class="mt-3 ml-1 p-2 fixed rounded-full bg-teal-500 shadow-lg">
                        <div class="p-2 rounded-full bg-teal-400">
                            <img src="./icons/event_note.svg" alt="call" 
                            class="w-8 h-8 cursor-pointer" on:click={()=> receptionBox = true}>
                        </div>
                    </div>
                </div>
            {/if}

            <!-- PHONE BUTTON -->
            <div transition:scale="{{ duration: 1000 }}" class="absolute bottom-0 right-0 w-20 h-20">
                <div class="mt-3 ml-2 p-2 fixed rounded-full bg-green-500 shadow-lg">
                    <div class="p-2 rounded-full bg-green-400">
                        <a href="tel:{service.phone}">
                            <img src="./icons/phone.svg" alt="call" class="w-8 h-8">
                        </a>
                    </div>
                </div>
            </div>

            <!-- RECEPTION BOX -->
            {#if receptionBox}
                <div id="receptionBox">
                    <div class="p-2 bg-gray-100 border rounded-lg shadow-lg">
                        <div class="text-center">
                            <span class="text-lg font-semibold">
                                Записаться
                            </span>
                        </div>
                        <div class="mt-1">
                            <div>
                                <span>
                                    Выберите услугу:
                                </span>
                            </div>
                            <div>
                                <select class="p-1 rounded-lg border bg-white" on:change={(e) => logSub = e.target.value}>
                                    <option value=""></option>
                                    {#each service.subServices as sub}
                                    <option value={sub}>{sub}</option>
                                    {/each}
                                </select>
                            </div>
                            <div class="mt-1">
                                <span>
                                    Выберите дату:
                                </span>
                            </div>
                            <div>
                                <input type="date" bind:value={logDate}>
                            </div>
                            <div class="mt-1">
                                <span>
                                    Выберите время:
                                </span>
                            </div>
                            <div>
                                <input type="time" bind:value={logTime}>
                            </div>
                            <div class="mt-1">
                                <span>
                                    Комментарий:
                                </span>
                            </div>
                            <div>
                                <input type="text" bind:value={logDesc}>
                            </div>
                        </div>
                        <div>
                            <button class="btn-gray" on:click={()=> receptionBox = false}>Отмена</button>
                            <button class="btn-orange" on:click={addNewRecord}>Записаться</button>
                        </div>
                    </div>
                </div>
            {/if}

            <!-- RECORDS BOX -->
            {#if recordsBox}
                <div id="recordsBox">
                    <div class="p-2 bg-gray-100 border rounded-lg shadow-lg">
                    <div class="text-center">
                        <span class=" text-lg font-semibold">
                            Журнал
                        </span>
                    </div>
                    <input type="date" bind:value={recDate}>
                    <Collection path={serviceRef.collection('records')} query={(ref) => ref.orderBy("time")} let:data={records} let:ref={recRef}>
                        {#each records as rec}
                            {#if rec.date == recDate}
                            <div class="mt-1 p-1 border rounded bg-white">
                                <div>
                                    <span class=" text-orange-600 font-semibold" on:click={()=> changeTime = !changeTime? true : false}>
                                        {rec.time}
                                    </span>
                                    <span class="ml-2 font-semibold">
                                        {rec.subservice}
                                    </span>
                                </div>
                                <div class="mt-1 text-sm">
                                    {rec.clientname}
                                    {rec.desc}
                                </div>
                                {#if !rec.confirmed}
                                    {#if changeTime}
                                        <div>
                                            <input type="time" bind:value={recTime}>
                                        </div>
                                    {/if}
                                <div class="mt-2 text-center">
                                    {#if recTime != undefined}
                                        <button class="btn-opt bg-orange-500" on:click={()=> rec.ref.update({time: recTime}).then(alertBoxCall('Время изменено.'))}>
                                            Изменить
                                        </button>
                                    {/if}
                                    <button class="btn-opt bg-green-500" on:click|once={()=> rec.ref.update({confirmed: true}).then(alertBoxCall('Принято.'))}>
                                        Принять
                                    </button>
                                    <button class="btn-opt bg-gray-800" on:click={()=> rec.ref.delete().then(alertBoxCall('Отменено.'))}>
                                        Отменить
                                    </button>
                                </div>
                                {/if}
                            </div>
                            {/if}
                        {/each}
                    </Collection>
                    <div class="mt-2 text-center">
                        <button class="btn-gray" on:click={()=> recordsBox = false}>Закрыть</button>
                    </div>
                    </div>
                </div>
            {/if}

            <span slot="fallback">Не удалось загрузить</span>
        </Doc>
    </User>
</FirebaseApp>
</div>

<!-- REPLY REVIEW -->
    {#if replyReviewBox}
        <div id="replyReviewBox" class=" fixed top-0">

            <div transition:fade="{{ duration: 200 }}" class="p-3 border bg-white rounded-lg shadow-lg">

                <p class="text-center">
                    <input type="text" placeholder="Ответ" bind:value={replyText}>
                </p>

                <p class="mt-2 text-center">
                    <button class="btn-gray" on:click={() => replyReviewBox = false}>Отмена</button>
                    <button class="btn-orange" on:click={() => replySend()}>Отправить</button>
                </p>

            </div>

        </div>
    {/if}
<!-- ALERT BOX -->
    {#if alertBox}
        <div id="alertBox" class=" fixed top-0">

            <div transition:fade="{{ duration: 500 }}" class="p-3 border bg-white rounded-lg shadow-lg">

                <p class="text-center">
                    {alertBoxContent}
                </p>

                <p class="mt-2 text-center">
