<script>
    import {firebase, db} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import {push} from 'svelte-spa-router';
    import { fade, fly } from 'svelte/transition';

    let isOpen = false;
    let email = '';
    let password = '';
    let phone ='+7';
    let username = '';
    let promocode = '';
    let promoRef;
    let promocodeExist = false;
    let usernameExist = 'Имя пользователя не менее 6 символов.';
    let newbalance;


    // AUTH USER and DB
    let getUser;
    let getUserAccount;

    firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
            // User is signed in.
            getUser = user;
        } else {
            // No user is signed in.
            console.log('no user login')
        }
    });

    function activatePromocode(code, uid) {
        let promoRef = db.collection('admin').doc('promo');
        let opt = {
            source: 'server'
        };
        promoRef.get(opt).then(function(doc) {
            if(doc.exists) {
                let data = doc.data();
                if(code in data.active) {
                    let userRef = db.collection('users').doc(uid);
                    userRef.update({
                        balance: data.active[code]
                    })
                    console.log('promocode activated!')
                    console.log('new balance: ', data.active[code])
                } else {
                    console.log('promocode not found')
                }
                console.log('doc.data', data)
                console.log('doc.active', data.active)
            } else {
                console.log('some error')
            }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
    }

    function usernameCheck() {
        if (username.length >= 6) {

            usernameExist = 'Имя пользователя свободно.';

            db.collection('users').where('username','==', username)
            .get()
            .then(function(querySnapshot) {
                querySnapshot.forEach(function(doc) {
                    // doc.data() is never undefined for query doc snapshots
                    // console.log(doc.id, " => ", doc.data());
                    usernameExist = 'Это имя занято, придумайте другое.'
                });
            })
            .catch(function(error) {
                console.log("Error getting documents: ", error);
            });
        } else {
            usernameExist = 'Имя пользователя не менее 6 символов.';
        }
    }

    function promocodeCheck() {
        if (promocode.length >= 6) {

            promocodeExist = false;

            db.collection('users').where('username','==', promocode)
            .get()
            .then(function(querySnapshot) {
                querySnapshot.forEach(function(doc) {
                    promoRef = doc.id;
                    promocodeExist = true;
                });
            })
            .catch(function(error) {
                console.log("Error getting documents: ", error);
            });
        } else {
            promocodeExist = false;
        }
    }

    function completeRegistration() {
        if (username.length  && promocode.length >= 6) {
            
            if (promocodeExist) {
                
                addNewUser()
                alert('Регистрация занимает несколько минут. Пока идёт регистрация, ознакомьтесь с полезной информацией.')
                push('/about')
                isOpen = false;
                  
            } else {
                alert('Неверный промокод!')
            }

        } else {
            
            alert('Придумайте имя пользователя и введите промокод.')
        }
    }

    // Cloud Functions
    let addNewUserFunction = firebase.functions().httpsCallable('addNewUser');

    // Call cloud functions
    function addNewUser() {

        addNewUserFunction({referrer: promoRef, username: username}).then(function(result) {
            console.log('registration', result)
        }).catch(function(error) {
            console.log(error.code, error.message, error.details)
        });
    }

</script>

<div class="header fixed z-10 h-16 top-0 w-full bg-gray-900">

    <!-- Logo -->
    <div class="justify-center flex flex-row items-center">
        <div class="mt-2 -ml-6">    
            <img class="w-12 h-12" src="./icons/main-logo.png" alt="logo">
        </div>
        <div class="ml-2 mt-1">
            <span class=" text-logo-red text-xl font-semibold">Avto</span><span class=" text-logo-green text-xl font-semibold">karta</span><span class=" text-logo-orange text-xl font-semibold">.club</span>
        </div>
    </div>

    <!-- BURGER BUTTON -->
    <div class="header-menu absolute top-0 right-0 p-2 text-center">
        {#if !isOpen}
            <img src="./icons/menu.svg" alt="menu" on:click={() => isOpen = true} class="mt-1 mr-1 w-10 h-10 cursor-pointer">
        {:else}
            <img src="./icons/close.svg" alt="menu" on:click={() => isOpen = false} class="mt-1 mr-1 w-10 h-10 cursor-pointer">
        {/if}
    </div>

    <!-- MENU -->
    {#if isOpen}
    <div transition:fade="{{ duration: 400 }}" class="menu-container">
        
        <div class="user-auth">
            <FirebaseApp {firebase}>
                <User let:user let:auth>
                    <!-- User info -->
                    <div class="user-info">
                        <span class=" text-logo-green">
                        {#if user.displayName != null}
                            {user.displayName}
                        {/if}
                        </span>
                    </div>
                    <!-- Account info -->
                    <div class="account-info">
                        <Doc path={`users/${user.uid}`} let:data>

                            <div slot="loading">
                                <p>загрузка...</p>
                            </div>

                            {#if data != undefined}
                            <div>
                                <span class=" text-logo-orange">

                                    Баланс: {data.balance}

                                </span>
                            </div>
                            {/if}
                            <div slot="fallback">
                                <div class="mt-2 p-1 bg-gray-100 text-teal-900">
                                    <p class="my-2 font-semibold text-red-600 text-lg"> Завершите регистрацию: </p>
                                    <p class=" text-sm"> Придумайте имя пользователя на латинице и введите промокод. </p>
                                    <input class="menu-input mt-2" type="text" placeholder="Имя пользователя" bind:value={username} on:input={usernameCheck}>
                                    <input class="menu-input" type="text" placeholder="Промокод" bind:value={promocode} on:input={promocodeCheck}>
                                    <span class=" text-sm block">{usernameExist}</span>
                                    <p>
                                        <button class="p-1 my-2 bg-teal-500 text-white rounded" on:click={completeRegistration}>Завершить</button>
                                    </p>
                                </div>
                            </div>
                            
                        </Doc>
                    </div>
                    <div>
                        <button class="p-1 px-3 mt-4 bg-gray-800 text-gray-200 rounded" on:click={()=> auth.signOut()}>Выйти</button>
                    </div>
                    <!-- Signed out -->
                    <div slot="signed-out">
                        <div>
                            <span class=" text-orange-500">
                                Для правильной работы приложения, необходимо авторизоваться.
                            </span>
                        </div>
                        
                    </div>
                </User>
            </FirebaseApp>
        </div>
        <br>
        <hr>

        <!-- MENU LIST -->
        <ul class="menu-list mt-4" on:click={ () => isOpen = false }>
            <li><a href="#/help">?</a></li>
            <li><a href="/">Карта</a></li>
            <li><a href={"#/profile"}>Профиль</a></li>
            <li><a href="#/about">Информация</a></li>
            <li><a href="#/addservice">Добавить сервис</a></li>
            <li><a href="#/cryptowallet">Крипто-кошелёк</a></li>
            <li><a href="#/partners">Партнерам</a></li>
            <li><a href="#/ico">ICO</a></li>
            
        </ul>
    </div>
    {/if}
</div>

<style>.menu-container {
  z-index: 50;
  padding: 0.5rem;
  width: 100%;
  height: 100vh;
  background-color: #1a202c;
  text-align: center;
}

.menu-list li {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-weight: 500;
  color: #a0aec0;
}

.menu-list li:hover {
  background-color: #2d3748;
}

@media (min-width: 1024px) {
  .menu-container {
    width: 25%;
    position: absolute;
    right: 0;
  }
}

