<script>
    import {firebase, db, googleProvider} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import { createEventDispatcher } from 'svelte';

    export let latlng;
    export let region;
    export let city;

    const dispatch = createEventDispatcher();

    function playSoundNewOrder() {
        let audio = new Audio('./sounds/sms-alert.mp3');
        audio.play();
    }

    function playSoundNotification() {
        let audio = new Audio('./sounds/notification.mp3');
        audio.play();
    }

    function goOnLine(uid) {
        db.collection('taxiDrivers').doc(uid)
        .update({
            online: true
        })
        .then(function() {
            alert("Вы на линии. Удачи!");
        })
        .catch(function(error) {
            // The document probably doesn't exist.
            console.error("Error updating document: ", error);
        });
    }

    function goOffline(uid) {

        db.collection('taxiDrivers').doc(uid)
        .update({
            online: false
        })
        .then(function() {
            alert("Работа завершена! Спасибо!");
        })
        .catch(function(error) {
            // The document probably doesn't exist.
            console.error("Error updating document: ", error);
        });
    }

    function setDriverLocation(uid) {
        db.collection('taxiDrivers').doc(uid)
        .update({
            location: [latlng.lat, latlng.lng]
        })
        .then(function() {
            alert("Вы отмечены на карте! Ожидайте клиентов!");
        })
        .catch(function(error) {
            // The document probably doesn't exist.
            console.error("Error updating document: ", error);
        });
    }

    function lookOrderPlace(ref, order) {

        dispatch('lookOrderPlace', {
            location: order.location
        });
        
    }

    function acceptOrder(ref, order, uid) {

        let orderRef = db.collection(ref).doc(order.id);

        db.runTransaction(function(transaction) {
            return transaction.get(orderRef).then(function(doc) {
                if (!doc.exists) {
                    throw "Document does not exist!";
                }

                let setDriver = doc.data().setDriver;
                setDriver = uid;
                if (setDriver == uid) {
                    transaction.update(orderRef, { setDriver: uid, active: false });
                    return setDriver;
                } else {
                    return Promise.reject("Ошибка!");
                }
            });
        }).then(function(setDriver) {

            playSoundNewOrder()
            
        }).catch(function(err) {
    
            console.error(err);
        });
    }

    function cancelOrder(ref, order, uid) {

        let orderRef = db.collection(ref).doc(order.id);

        let removeSetDriver = orderRef.update({
            active: true,
            setDriver: firebase.firestore.FieldValue.delete()
        });
    }

    function driverArrived(ref, order, uid) {
        dispatch('driverArrived', {
            order: order,
            ref: ref
        });
    }

    function completeOrder(ref, order, uid) {

        let orderRef = db.collection(ref).doc(order.id);

        if (order.paymentMethod === 'Автокарта') {

            orderRef.update({
                completed: true
            });
    
            completeTaxiOrder(ref, order.id, order.clientUID, order.price);

        } else {

            orderRef.update({
                completed: true,
                payed: true
            });
        }
        
    }

    // Cloud Functions
    let completeTaxiOrderFunction = firebase.functions().httpsCallable('completeTaxiOrder');

    // Call cloud functions
    function completeTaxiOrder(orderRef, orderID, clientUID, price) {
        
        completeTaxiOrderFunction({orderRef, orderID, clientUID, price})
        .then(function(result) {
            console.log('completeTaxiOrder', result)
        }).catch(function(error) {
            console.log(error.code, error.message, error.details)
        });
    }

</script>

<FirebaseApp {firebase}>
    <User let:user>
        <Collection 
        path={`taxiOrders/region/${region}/city/${city}`} 
        query={ (ref) => ref.orderBy("date").limit(10) } 
        let:data={orders} 
        let:ref
        on:data={playSoundNotification}>

            <div class="taxi-orders-container z-0 fixed ml-12 mr-2">
                <div class="p-2 bg-gray-100 rounded-lg shadow-lg h-auto w-full">

                    <!-- CONTROL BUTTONS -->
                    <div class=" text-center">
                        <button class="p-1 bg-green-500 text-sm text-white rounded shadow" 
                        on:click={() => goOnLine(user.uid)}>
                        Выйти на линию
                        </button>

                        <button class="p-1 mt-1 bg-red-500 text-sm text-white rounded shadow" 
                        on:click={() => goOffline(user.uid)}>
                        Закончить работу
                        </button>
                        
                        <button class="p-1 mt-1 bg-orange-500 text-sm text-white rounded shadow" 
                        on:click={() => setDriverLocation(user.uid)}>
                        Отметиться на карте
                        </button>
                    </div>

                    <p class="mt-3 font-semibold text-center"> Текущие заказы: </p> 
                    <hr>
                    
                    <!-- ORDERS -->
                    <div class=" h-80 overflow-y-auto">
                        <ul>
                            {#each orders as order}
                            {#if order.payed != true}

                            <!-- ORDER -->
                            <li>

                            <!-- ORDER DETAILS -->
                            <div class="mt-2 text-sm">
                                {#if order.setDriver == user.uid && order.completed != true}
                                    <p class=" font-semibold text-lg text-green-600"> Заказ принят! Направляйтесь к клиенту!</p>
                                {/if}
                                <p>
                                <span class="font-semibold"> Откуда: </span>  {order.address} 
                                </p>
                                <p>
                                <span class="font-semibold"> Куда: </span> {order.routes} 
                                </p>
                                <p>
                                <span class="font-semibold"> Примечание: </span> {order.desc} 
                                </p>
                                {#if order.childSeat}
                                    <p><span class="font-semibold text-red-600"> Детское кресло! </span> </p>
                                {/if}
                                <p>
                                <span class="font-semibold text-orange-500"> Цена:  {order.price} руб.</span>
                                </p>
                                <p>
                                <span class="font-semibold text-green-500"> Способ оплаты:  {order.paymentMethod}</span>
                                </p>
                            </div>

                            <!-- ORDER BUTTONS -->
                            {#if order.completed == undefined || false}
                                <div class="mt-3 mb-2 text-center flex flex-row justify-around">

                                    <div>
                                        <button class="order-btn bg-teal-400" on:click={lookOrderPlace(ref.path, order)}>
                                            <img class="w-8 h-8" src="./icons/place.svg" alt="placeMarker">
                                        </button>
                                    </div>
                                    
                                    <div>
                                        <button class="order-btn bg-green-400">
                                            <a href="tel:{order.phone}">
                                                <img class="w-8 h-8" src="./icons/phone.svg" alt="placeMarker">
                                            </a>
                                        </button>
                                    </div>
                                    
                                    <div>
                                        {#if order.setDriver}
                                        <button class="order-btn bg-red-500" on:click={cancelOrder(ref.path, order, user.uid)}>
                                            <img class="w-8 h-8" src="./icons/close_white.svg" alt="getOrder">
                                        </button>
                                        {:else}
                                        <button class="order-btn bg-white" on:click={acceptOrder(ref.path, order, user.uid)}>
                                            <img class="w-8 h-8" src="./icons/check.svg" alt="getOrder">
                                        </button>
                                        {/if}
                                    </div>
                                </div>
                            {/if}
                            <hr>
                            <!-- Arrived & complete buttons -->
                            <div class="my-2 flex flex-row justify-around">
                                {#if order.setDriver == user.uid && order.driverArrived != true || undefined}
                                    <div class=" text-center">
                                        <button on:click={driverArrived(ref.path, order, user.uid)} class="btn-orange"> 
                                        На месте 
                                        </button>
                                    </div>
                                {/if}
                                {#if order.onway && order.completed != true}
                                    <div class=" text-center">
                                        <button on:click|once={completeOrder(ref.path, order, user.uid)} class=" text-white bg-red-600"> 
                                        Завершить 
                                        </button>
                                    </div>
                                {/if}
                            </div>

                            <!-- Completed -->
                            {#if order.completed}
                                <p class=" text-gray-700"> Заказ завершен. Баланс обновится через несколько минут.</p>
                            {/if}
                            <hr>
                                
                            </li>
                            {/if}
                            {/each}
                        </ul>
                    </div>
                </div>
            </div>

            <div slot="loading">
                <div class="p-2 ml-20 rounded-lg bg-gray-100 shadow-lg">
                    Загрузка...
                </div>
            </div>
 
            <div slot="fallback">
                <div class="p-2 ml-20 rounded-lg bg-gray-100 shadow-lg">
                    Невозможно загрузить данные...
                </div>
            </div>

        </Collection>
    </User>
</FirebaseApp>

<style>.order-btn {
  padding: 0.5rem;
  border-radius: 9999px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  outline: 0;
}

/*# sourceMappingURL=data:application/json;base64, */</style>