    // -----------
    // MAP EVENT ZOOM
    // 
        function mapZoomed(e) {
            currentZoom = e.target._zoom;
            // console.log('zoom', e.target._zoom);
            LatLng = map.getCenter();
            // console.log('latlng', LatLng);
            mapBounds = map.getBounds();
            // console.log('mapBounds', mapBounds);

            if (currentZoom <= 11) {
                allLayers.forEach(layer => {
                    layer.remove();
                });
            }

            else if (currentZoom >= 17) {

                allLayers.forEach(layer => {
                    layer.remove();
                });

                allLayers.forEach(layers => {
                    
                    layers.eachLayer(function (layer) {
                        layer.options.icon.options.iconSize = [50, 50]
                    });

                    layers.addTo(map);
                });



            } else if (currentZoom < 15) {

                allLayers.forEach(layer => {
                    layer.remove();
                });

                allLayers.forEach(layers => {
                    
                    layers.eachLayer(function (layer) {
                        layer.options.icon.options.iconSize = [30, 30]
                    });

                    layers.addTo(map);
                });

            } else if (currentZoom < 18) {

                allLayers.forEach(layer => {
                    layer.remove();
                });

                allLayers.forEach(layers => {
                    
                    layers.eachLayer(function (layer) {
                        layer.options.icon.options.iconSize = [40, 40]
                    });

                    layers.addTo(map);
                });
            }
        }
    // --------------------
    // MAP EVENT "mapMoved"
    // 
        function mapMoved(e) {
            
            let lastRegion = region;
            let lastDistrict = district;

            if (getUserAccount && getUserAccount.premium) {
                
                LatLng = map.getCenter();
                getRegion();        
            }

            function getRegion() {
                geocodingClient.reverseGeocode({
                query: [LatLng.lng, LatLng.lat]
                })
                .send()
                .then(response => {
                    const result = response.body;
                    const resarr = result.features;

                    resarr.forEach(el => {
                        if (el.place_type[0] === 'region') {
                            region = el.text;
                        }
                        else if (el.place_type[0] === 'district') {
                            district = el.text;
                        }
                        else if (el.place_type[0] === 'place') {
                            city = el.text;
                        }
                    });

                    let hasDistrict = resarr.some(el => {
                        return el.place_type[0] === 'district';
                    });

                    if (!hasDistrict) {
                        district = city;
                    }
                    
                    if (region != lastRegion || district != lastDistrict) {

                        allLayers.forEach(layer => {
                            layer.remove();
                        });

                        getMapInfo()
                    }
                });
            }

        }
    // ------------------
    // FULLSCREEN TOGGLE
    // 
        function toggleFullscreen(id) {

            let elem = document.getElementById(id);

            if (!document.fullscreenElement) {
                elem.requestFullscreen().catch(err => {
                alertBoxCall(`Error attempting to enable full-screen mode: ${err.message} (${err.name})`);
                });
            } else {
                document.exitFullscreen();
            }
        }

    // ----------
    // ALERT BOX
    // 
        function alertBoxCall(mes) {

            if (!alertBox) {

                alertBoxContent = mes;
                alertBox = true;
            }
            setTimeout(()=>{
                alertBox = false;
            }, 5000)
        }

