    // 
        function setUserLocationMarker(e) {
            if (userLocationMarker == null || undefined) {
                userLocationMarker = L.marker(e.latlng, {icon: placeholderIcon, rotationAngle: 0})
                .addTo(map)
                .bindPopup("Ты здесь!");

                userLocation.latlng = e.latlng;
                
                getCurrentRegion()
            }
        }
    
    // --------------
    // Rotate marker
    // 
        function rotateDirectionMarker(e) {

            let oldLatLng = e.oldLatLng;
            let newLatLng = e.latlng;
            let angle = bearingBetweenLocations(oldLatLng, newLatLng);

            function bearingBetweenLocations(oldLatLng, newLatLng) {

                let PI = 3.14159;
                let lat1 = oldLatLng.lat * PI / 180;
                let long1 = oldLatLng.lng * PI / 180;
                let lat2 = newLatLng.lat * PI / 180;
                let long2 = newLatLng.lng * PI / 180;

                let dLon = (long2 - long1);

                let y = Math.sin(dLon) * Math.cos(lat2);
                let x = Math.cos(lat1) * Math.sin(lat2) - Math.sin(lat1)
                        * Math.cos(lat2) * Math.cos(dLon);

                let brng = Math.atan2(y, x);

                brng = brng * (180 / PI);
                brng = (brng + 360) % 360;

                return brng;
            }

            userLocationMarker.setRotationAngle(angle);
        }

    // ---------------------
    // Target user location
    // 
        function targetUserLocation(e) {
            userLocationMarker.setLatLng(e.latlng);
            userLocation = {
                latlng: e.latlng,
                region: region,
                district: district,
                city: city
            }
        }

    // --------------------
    // Watch user location
    // 
        let watchUserLoc = false;

        function watchUserLocation() {
            if (!watchUserLoc) {
                watchUserLoc = true;
                userLocationMarker.setIcon(directionIcon);
                userLocationMarker.on('move', rotateDirectionMarker);

                map.locate({watch: true, setView: true, enableHighAccuracy: true, maxZoom: 17});
                
                alertBoxCall('Следовать за маркером включено.')
                
            } else {
                watchUserLoc = false;
                userLocationMarker.setIcon(placeholderIcon);
                map.stopLocate();
                userLocationMarker.setRotationAngle(0);
                alertBoxCall('Следовать за маркером выключено.')
            }
        }

    // ------------------
    // MAP EVENT "contextmenu"
    // 
        function onContextmenu(e) {
            LatLng = e.latlng;
        }

    // ----------
    // ADD ICONS
    // 
        // Add SOS icon
        function addSosIcon(e) {
            console.log(e.detail.message, e.detail.type, e.detail.phone)

            if (userLocation.city === city && userLocation.district === district && userLocation.region === region) {
                
                db.collection(`icons/sos/${region}`).add({
                    city: city,
                    district: district,
                    added: firebase.firestore.FieldValue.serverTimestamp(),
                    location: [LatLng.lat, LatLng.lng],
                    author: getUser.uid,
                    message: e.detail.message,
                    phone: e.detail.phone
                })
                .then(function(docRef) {
                    getMapInfo()
                    alertBoxCall('Добавлено.');
                })
                .catch(function(error) {
                    console.error("Error adding document: ", error);
                });
            }
        }

        // Add Icon to map
        function addMapIcon(collection) {

            let iCount = 0;

            db.collection(`icons/${collection}/${region}`).where("author", "==", getUser.uid)
            .get()
            .then(function(querySnapshot) {
                iCount = querySnapshot.size;
                if (iCount >= 5) {
                    alertBoxCall('Вы добавили максимальное кол-во иконок данного типа.');
                } else {
                    
                    checkRating();
                }
            })

            function checkRating() {
                
                if (getUserAccount.mapRating > 5 ) {
                    add();
                } else {
                    alertBoxCall('Ваш рейтинг слишком мал.');
                }
            }
            
            function add() {

                if (userLocation.city === city && userLocation.district === district && userLocation.region === region) {
                    
                    db.collection(`icons/${collection}/${region}`).add({
                        city: city,
                        district: district,
                        added: firebase.firestore.FieldValue.serverTimestamp(),
                        location: [LatLng.lat, LatLng.lng],
                        author: getUser.uid,
                        actual: 0.7,
                        voters: [],
                        votes: []
                    })
                    .then(function(docRef) {
                        getMapInfo()
                        alertBoxCall('Добавлено.');
                    })
                    .catch(function(error) {
                        console.error("Error adding document: ", error);
                    });
    
                }
                else {
                    alertBoxCall('Вы находитесь слишком далеко! Обновите местоположение.');
                }
            }
            
        }

    // ----------------------
    // MAP EVENT MarkerClick
    // 

        function onMarkerClick(e) {
            clickedMarkerId = e.target.options.alt;
            clickedMarkerPath = e.target.options.title;
            contextMenuDialog = true;
        }

        function markerTrue() {

            let markerRef = db.collection(`${clickedMarkerPath}`).doc(`${clickedMarkerId}`);

            markerRef.get()
            .then(function(doc) {

                const data = doc.data();
                let voted = data.voters.some(el=>{
                    return el === getUser.uid
                });
                const votes = data.votes;

                if (!voted) {
                    if (votes.length > 0) {
                        let last = votes[votes.length - 1]
                        if (last > 0) {
                            update(1.2 + Math.random() / 10)
                        } else {
                            update(1 + Math.random() / 10)
                        }
                    } else {

                        update(1);
                    }
                } 
                else {
                    alertBoxCall("Вы уже проголосовали!")
                }
                
            }).catch(function(error) {
                alertBoxCall(`Ошибка: ${error.message}`);
            });

            function update(vote) {
                markerRef.update({
                    actual: firebase.firestore.FieldValue.increment(0.11),
                    voters: firebase.firestore.FieldValue.arrayUnion(getUser.uid),
                    votes: firebase.firestore.FieldValue.arrayUnion(vote)
                })
                .then(function() {
                    alertBoxCall('Голос принят!');
                    checkMarkerRating(markerRef);
                })
                .catch(function(error) {
                    alertBoxCall(`Ошибка: ${error.message}`);
                });
            }

        }

        function markerFalse() {

            let markerRef = db.collection(`${clickedMarkerPath}`).doc(`${clickedMarkerId}`);

            markerRef.get()
            .then(function(doc) {
                
                let data = doc.data();
                let voted = data.voters.some(el=>{
                    return el === getUser.uid
                });
                const votes = data.votes;

                if (!voted) {
                    if (votes.length > 0) {
                        let last = votes[votes.length - 1]
                        if (last < 0) {
                            update(-1.2 - Math.random() / 10)
                        } else {
                            update(-1 - Math.random() / 10)
                        }
                    } else {

                        update(-1);
                    }
                } else {
                    alertBoxCall("Вы уже проголосовали!")
                }
                
            }).catch(function(error) {
                alertBoxCall(`Ошибка: ${error.message}`);
            });

            function update(vote) {
                markerRef.update({
                    actual: firebase.firestore.FieldValue.increment(-0.11),
                    voters: firebase.firestore.FieldValue.arrayUnion(getUser.uid),
                    votes: firebase.firestore.FieldValue.arrayUnion(vote)
                })
                .then(function() {
                    alertBoxCall('Голос принят!');
                    checkMarkerRating(markerRef);
                })
                .catch(function(error) {
                    alertBoxCall(`Ошибка: ${error.message}`);
                });
            }

        }

        function checkMarkerRating(markerRef) {

            markerRef.get().then(function(doc) {
                const data = doc.data();
                const votes = data.votes;

                if(votes.length === 5) {

                    const sum = votes.reduce((a,b)=> a + b, 0);
                    const average = sum / votes.length;
                    console.log( 'ave', average, 'sum', sum)

                    if (average < 0) {
                        ratingDecrement(data.author)
                        console.log('dec')
                    }
                    else if (average > 1) {
                        ratingIncrement(data.author)
                        console.log('inc')
                    }
                }

            }).catch(function(error) {
                console.log("Error getting document:", error);
            });
        }

        function ratingDecrement(author) {
            let ratingDecrementFunc = firebase.functions().httpsCallable('ratingDecrement');

            ratingDecrementFunc({author})
            .then(function(result) {
                console.log('result', result.data)
            }).catch(function(error) {
                console.log(error.code, error.message, error.details)
            });
        }

        function ratingIncrement(author) {
            let ratingIncrementFunc = firebase.functions().httpsCallable('ratingIncrement');

            ratingIncrementFunc({author})
            .then(function(result) {
                console.log('result', result.data)
            }).catch(function(error) {
                console.log(error.code, error.message, error.details)
            });
        }

    // -------------
    // Search field
    // 
        let searchVal;
        let sapi = axios.create({baseURL: 'https://nominatim.openstreetmap.org/search/'})
    
    // -------------
    // Search place
    // 
        async function searchClicked() {

            try {
                const mb = {
                    swLng: mapBounds._southWest.lng - 0.02,
                    neLat: mapBounds._northEast.lat + 0.02,
                    neLng: mapBounds._northEast.lng + 0.02,
                    swLat: mapBounds._southWest.lat - 0.02
                };
                
                const response = await sapi.get(searchVal, {
                    params: {
                        format: 'json',
                        limit: 3,
                        addressdetails: 1,
                        countrycodes: ['ru'],
                        viewbox: `${mb.swLng}, ${mb.neLat}, ${mb.neLng}, ${mb.swLat}`,
                        bounded: 1
                    }
                });

                const data = response.data;

                if (data.length >= 1) {

                    const coors = [data[0].lat, data[0].lon];
        
                    data.forEach(el => {
                        L.marker([el.lat, el.lon], {icon: locationIcon}).addTo(map).bindPopup(`${el.display_name}`);
                    });
                    
                    map.flyTo(coors, 16)

                } else {
                    alertBoxCall('Ничего не найдено. Попробуйте отдалить карту или изменить запрос.');
                }
            } catch (error) {
                console.error(error);
            }

        }

    // --------------------
    // GET address & POPUP
    // 
        async function getPlaceAddress() {
            const response = await fetch(`https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${LatLng.lat}&lon=${LatLng.lng}`);
            const data = await response.json();
            const address = data.display_name;
            
            L.popup()
            .setLatLng(LatLng)
            .setContent(address)
            .openOn(map);
        }

    // ---------------------------------------------------------
    // Get address from clicked place on map with current LatLng
    // 
        async function getClickAddress() {
            const response = await fetch(`https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${LatLng.lat}&lon=${LatLng.lng}`);
            const data = await response.json();
            clickedAddress = data.display_name;
        }

    // -----------------------
    // GET current region info
    // 
        function getCurrentRegion() {
            geocodingClient.reverseGeocode({
            query: [LatLng.lng, LatLng.lat]
            })
            .send()
            .then(response => {
                const result = response.body;
                const resarr = result.features;

                resarr.forEach(el => {
                    if (el.place_type[0] === 'region') {
                        region = el.text;
                    }
                    else if (el.place_type[0] === 'district') {
                        district = el.text;
                    }
                    else if (el.place_type[0] === 'place') {
                        city = el.text;
                    }
                });

                let hasDistrict = resarr.some(el => {
                    return el.place_type[0] === 'district';
                });

                if (!hasDistrict) {
                    district = city;
                }
                
                userLocation.region = region;
                userLocation.city = city;
                userLocation.district = district;

                getMapInfo()
            });
        }

    // ----------------
    // call TAXI dialog
    // 
        let callTaxiDialog = false;
        async function callTaxi() {

            if (getUserAccount.balance >= 100) {

                const response = await fetch(`https://nominatim.openstreetmap.org/reverse?format=jsonv2&lat=${LatLng.lat}&lon=${LatLng.lng}`);
                const data = await response.json();
                clickedAddress = data.display_name;
                callTaxiDialog = true;

            } else {
                alertBoxCall('На вашем счете недостаточно средств. Менее 100 руб.')
            }
        }

    // ---------------
    // TAXI ORDERS BOX
    // 
        let taxiOrdersBox = false;
        function taxiDriverCheck() {

            if (!taxiOrdersBox) {

                db.collection('taxiDrivers').doc(getUser.uid)
                .get()
                .then(function(doc) {
                    if (doc.exists) {

                        taxiOrdersBox = true;

                        db.collection(`taxiOrders/region/${region}/city/${city}`).where("active", "==", true)
                        .get()
                        .then(function(querySnapshot) {
                            querySnapshot.forEach(function(doc) {
                                let data = doc.data();
                            
                                L.marker(data.location, {icon: taxiMarkerIcon, alt: doc.id})
                                .addTo(map)
                                .bindPopup(`<p> Пункт назначения: ${data.routes}</p> <p> Примечание: ${data.desc}</p> <p> Цена: ${data.price}</p> <a href="tel:${data.phone}"> Позвонить клиенту </a>`);
                                
                            });
                        });
                    } else {
                        alertBoxCall('Список текущих заказов доступен только для зарегистрированных водителей.')
                    }
                }).catch(function(error) {
                    console.log("Error getting document:", error);
                });
            } else {
                taxiOrdersBox = false;
            }
        }

    // -----
    // flyTo
    // 
        function flyTo(e) {
            map.flyTo(e.detail.location, 16)
            console.log(e.detail)
        }

    // ----------------
    // Client TAXI BOX
    // 
        let clientTaxiBox = false;
        
        function TaxiClientBox() {

            if (!clientTaxiBox) {

                clientTaxiBox = true;

            } else {
                clientTaxiBox = false;
            }
        }

    // --------
    // CHAT BOX
    // 
        let chatBox = false;

        function openChat() {

            if (!chatBox) {

                chatBox = true;
            
            } else {
                chatBox = false;
            
            }
        }

    // --------------
    // TAXI FUNCTIONS
    // 
        function checkDriverArrived(e) {
            let distance = map.distance(e.detail.order.location, userLocation.latlng)
            console.log('distance', distance)
            if (distance <= 150) {
                db.collection(e.detail.ref).doc(e.detail.order.id).update({
                    driverArrived: true
                })
                .then(function() {
                    alertBoxCall("Клиент оповещен! Ожидайте.");
                })
                .catch(function(error) {
                    // The document probably doesn't exist.
                    console.error("Error updating document: ", error);
                });

            } else {
                alertBoxCall('Вы слишком далеко! Если вы на месте обновите локацию.')
            }
        }

        function checkClientOnWay(e) {
            let distance = map.distance(e.detail.order.location, userLocation.latlng)
            console.log('distance', distance)
            if (distance >= 10) {
                db.collection(e.detail.ref).doc(e.detail.order.id).update({
                    onway: true
                })
                .then(function() {
                    alertBoxCall("Поехали. Не забудьте пристегнуться!");
                })
                .catch(function(error) {
                    // The document probably doesn't exist.
                    console.error("Error updating document: ", error);
                });

            } else {
                console.log('waiting')
            }
        }

    // -------------
    // CONTEXT MENU
    // 
        let contextmenu = false;

        function onContextMenu(e) {

            e.preventDefault();

            let menu = document.getElementById('contextmenu');
        
            menu.style.left = e.clientX  + 'px';
            menu.style.top = e.clientY + 'px';

            contextmenu = true;
        }

    // ----
    // SOS BOX
    // 
        let sosBox = false;

        function sos() {

            if (!sosBox) {
                sosBox = true;
            }
        }

    // ------------
    // ADD SERVICE BOX
    // 
        let addTopicBox;

        function addTopic() {
            if (currentZoom < 18) {
                alertBoxCall('Увеличьте зум до максимума и повторите попытку.');
            } else {
                addTopicBox = true;
            }
        }
    
    // -----------
    // RENTAL CAR
    // 
        let rentalCarBox;

        function openRentalCar() {
            if (!rentalCarBox) {
                rentalCarBox = true;
            } else {
                rentalCarBox = false;
            }
        }

    // ---------------------------------

</script>

<svelte:window on:contextmenu={onContextMenu}/>

<div id="main" class="relative">

    <!-- MAP  -->
    <div id="map" style="min-height: 550px;" class="z-0 w-full h-screen">
    </div>

    <!-- MENU BUTTONS ON MAP  -->
    <div class="absolute top-0 mt-16 ml-1">
        <div class="flex flex-col">

            <!-- Search button -->
            <div class="mt-2 relative">
                <button class="map-btn" type="button" on:click={()=> searchField = !searchField ? true : false}>
                    <img src="./icons/search.png" alt="targetLocation">
                </button>
                {#if searchField}
                    <div transition:fly="{{x: -300, duration: 1000 }}" class="fixed ml-12 -mt-10 bg-white rounded shadow-lg flex flex-row">
                        <div>
                            <input class="p-1 w-full rounded outline-none" type="text" placeholder="Адрес" bind:value={searchVal}>
                        </div>
                        <div>
                            <button class="p-1 w-full text-white bg-gray-800 rounded" type="button" on:click={searchClicked}> Найти </button>
                        </div>
                    </div>
                {/if}
            </div>

            <!-- Fullscreen button -->
            <div class="mt-2">
                <button class="map-btn" type="button" on:click={()=> toggleFullscreen('main')}>
                    <img src="./icons/fullscreen.png" alt="fullscreen">
                </button>
            </div>

            <!-- Location target button -->
            <div class="mt-2">
                <button class="map-btn" type="button" on:click={watchUserLocation}>
                    <img src="./icons/target.png" alt="targetLocation">
                </button>
            </div>

            <!-- Client taxi button -->
            <div class="mt-2">
                <button class="map-btn" type="button" on:click={TaxiClientBox}>
                    <img src="./icons/taxi-find.png" alt="taxiLocation">
                </button>
            </div>

            <!-- Get taxi orders button -->
            <div class="mt-2">
                <button class="map-btn" type="button" on:click={taxiDriverCheck}>
                    <img src="./icons/taxi-signal.png" alt="taxiOrders">
                </button>
            </div>

            <!-- Get taxi orders button -->
            <div class="mt-2">
                <button class="map-btn" type="button" on:click={openRentalCar}>
                    <img src="./icons/excavator.png" alt="excavator">
                </button>
            </div>

            <!-- Chat button -->
            <div class="mt-2">
                <button class="map-btn" type="button" on:click={openChat}>
                    <img src="./icons/chat-phone.png" alt="chat">
                </button>
            </div>
        </div>
    </div>

    <!-- TAXI ORDERS -->
    <div class=" absolute top-0 mt-16">
        {#if taxiOrdersBox}
            {#if userLocation != undefined && userLocation.region != undefined}
                <TaxiOrders region={userLocation.region} city={userLocation.city} latlng={userLocation.latlng} 
                on:lookOrderPlace={flyTo} on:driverArrived={checkDriverArrived}/>
            {/if}
        {/if}
    </div>

    <!-- CALL TAXI DIALOG -->
    <div class=" absolute top-0 mt-16">
        {#if callTaxiDialog}
            
            <TaxiCallDialog address={clickedAddress} {LatLng} clientUID={getUser.uid} clientName={getUser.displayName}
            region={userLocation.region} city={userLocation.city} 
            on:closeDialog={() => callTaxiDialog = false} on:alert={(e) => alertBoxCall(e.detail.message)}/>
            
        {/if}
    </div>

    <!-- CLIENT TAXI BOX -->
    <div id="clientTaxiBox" class=" absolute top-0 mt-16">
        {#if clientTaxiBox}
            {#if userLocation != undefined && userLocation.region != undefined}
                <TaxiClient region={userLocation.region} city={userLocation.city} on:findDriverLocation={flyTo}
                on:checkOnWay={checkClientOnWay} on:alert={(e) => alertBoxCall(e.detail.message)}
                on:close={()=> clientTaxiBox = false}/>
            {/if}
        {/if}
    </div>

    <!-- CHAT BOX -->
    <div id="Chat" class=" absolute top-0 mt-16">
        {#if chatBox}
            {#if userLocation != undefined && userLocation.region != undefined}
                <Chat region={userLocation.region} district={userLocation.district} city={userLocation.city} 
                on:fullscreen={()=> toggleFullscreen('Chat')}/>
            {/if}
        {/if}
    </div>

    <!-- RENTAL CAR BOX -->
    {#if rentalCarBox}
        <div id="rentalCarBox" class="absolute top-0 mt-16 w-full bg-white">
            <RentalCar region={userLocation.region} city={userLocation.city} latlng={userLocation.latlng} 
            on:close={()=> rentalCarBox = false} on:alert={(e)=> alertBoxCall(e.detail.message)}/>
        </div>
    {/if}

    <!-- ADD SERVICE BOX -->
    {#if addTopicBox}
        <div id="addServiceBox" class="absolute top-0 mt-16 w-full">
            <AddTopic user={getUser} region={userLocation.region} city={userLocation.city} latlng={LatLng} 
            on:close={()=> addTopicBox = false} on:alert={(e)=> alertBoxCall(e.detail.message)}/>
        </div>
    {/if}

    <!-- SOS BOX -->
    {#if sosBox}
        <div id="sosBox" class="absolute top-0 mt-16 w-full">
            <Sos region={userLocation.region} {getUserAccount} on:danger={addSosIcon} on:close={()=> sosBox = false}/>
        </div>
    {/if}

    <!-- ALERT BOX -->
    {#if alertBox}
        <div id="alertBox" class=" fixed top-0">

            <div transition:fade="{{ duration: 500 }}" class="p-3 border bg-white rounded-lg shadow-lg">

                <p class="text-center">
                    {alertBoxContent}
                </p>

                <p class="mt-2 text-center">
                    <button class="btn-orange" on:click={() => alertBox = false}>Ок</button>
                </p>

            </div>

        </div>
    {/if}

    <!-- CONTEXT MENU DIALOG -->
    <div id="marker-dialog">
        {#if contextMenuDialog}
            <div id="dialog" class=" flex flex-row items-center justify-around" on:click={()=> contextMenuDialog = false}>

                <div transition:fly={{x:-100, duration: 1000}}>
                    <button class="m-2 p-3 w-16 h-16 border border-black rounded-full bg-green-500" on:click={markerTrue}>
                        <img class="object-cover" src="./icons/like.png" alt="like">
                    </button>
                </div>

                <div transition:scale={{duration: 1000}}>
                    <button class="m-2 p-0 w-12 h-12 bg-transparent rounded-full" on:click={()=> contextMenuDialog = false}>
                        <img class="object-cover" src="./icons/button-close-yellow.png" alt="close">
                    </button>
                </div>

                <div transition:fly={{x:100, duration: 1000}}>
                    <button class="m-2 p-3 w-16 h-16 border border-black rounded-full bg-red-500" on:click={markerFalse}>
                        <img class="object-cover" src="./icons/dislike.png" alt="dislike">
                    </button>
                </div>

            </div>
        {/if}
    </div>

    <!-- CONTEXT MENU -->
    <div id="contextmenu" class="absolute">
        {#if contextmenu}
            <ContextMenu on:closeMenu={()=> contextmenu = false}
            on:getAddress={getPlaceAddress}
            on:police={()=> addMapIcon('police')}
            on:mCamera={()=> addMapIcon('mCamera')}
            on:sCamera={()=> addMapIcon('sCamera')}
            on:dtp={()=> addMapIcon('DTP')}
            on:rClosed={()=> addMapIcon('roadClosed')}
            on:rConstruction={()=> addMapIcon('roadConstruction')}
            on:taxiCall={callTaxi} 
            on:sos={sos}
            on:add={addTopic}
            />
        {/if}
