<script>
    import {fade} from 'svelte/transition';
    import {firebase, db, storage} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection, StorageRef} from 'sveltefire';

    let storageRef = storage.ref();
    let getUser;
    let carModel, carType, carCategory, carImageURL;
    let anotherType = '';
    let rentPrice, phone;
    let alertBox, alertBoxContent;
    let regBox = false;

    

    firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
            getUser = user;
        } else {
            // No user is signed in.
        }
    });

    function carImageUpload(e, uid) {
        e.stopPropagation();
        e.preventDefault();
        
        let file = e.target.files[0];

        let metadata = {
            'contentType': file.type
        };

        storageRef.child(`users/${uid}/rentalCar/${file.name}`)
        .put(file, metadata)
        .then(function(snapshot) {
            console.log('Uploaded', snapshot.totalBytes, 'bytes.');
            console.log('File metadata:', snapshot.metadata);
            // Let's get a download URL for the file.
            snapshot.ref.getDownloadURL().then(function(url) {
                console.log('File available at', url);

                carImageURL = url;
            });
        })
        .catch(function(error) {
            // [START onfailure]
            console.error('Upload failed:', error);
            // [END onfailure]
        });
       
    }

    function regRentalCar() {
        
        if (carCategory && carType && carModel && rentPrice && phone && carImageURL != undefined) {

            let newCar = {
                owner: getUser.uid,
                username: getUser.displayName,
                category: carCategory,
                type: carType,
                atype: anotherType,
                model: carModel,
                price: rentPrice,
                phone: phone,
                imgURL: carImageURL
            }

            db.collection("rentalCars").add(newCar)
            .then(function(docRef) {
                alertBoxCall('Спецтехника зарегистрирована.');
            })
            .catch(function(error) {
                alertBoxCall(`Ошибка: ${error}`);
            });

        } else {
            alertBoxCall('Заполните все поля и загрузите фото спецтехники.');
        }
    }

    // ALERT BOX
    function alertBoxCall(mes) {

        if (!alertBox) {

            alertBoxContent = mes;
            alertBox = true;
        }
        setTimeout(()=>{
            alertBox = false;
        }, 5000)
    }


</script>

<div class="p-2 bg-gray-100">
    <FirebaseApp {firebase}>
        <User let:user>
            <div>
                <p class=" text-teal-500 text-center font-semibold">
                    Аренда и услуги спецтехники
                </p>
            </div>

            <div class="mt-3">
                <Collection path={'rentalCars'} query={(ref) => ref.where("owner", "==", user.uid)} let:data={cars}>
                    {#if cars.length > 0 }
                        {#each cars as car}
                            
                            <div class="flex flex-row bg-white rounded-lg justify-between items-center overflow-hidden">
                                <div class="">
                                    <img src={car.imgURL} alt="foto" class="w-20 h-20">
                                </div>
                                <div>
                                    <span class=" text-teal-500">
                                        {car.model}
                                    </span>
                                </div>
                                <div>
                                    
                                    <button class="btn-orange" on:click={()=> car.ref.delete()}>Удалить</button>
                                    
                                </div>
                            </div>
                        {/each}
                        <p class="mt-3 text-center">
                            <button class="btn-green" on:click={()=> regBox = true}>Добавить</button>
                        </p>
                    {:else}
                        <p>
                            <button class="btn-green" on:click={()=> regBox = true}>Добавить</button>
                        </p>
                    {/if}

                    <span slot="fallback">Не удалось загрузить...</span>
                </Collection>
            </div>

            <!-- REGISTRATION -->
            {#if regBox}
            <div class="mt-2 p-1 bg-white rounded-lg">
                <div>
                    <p class=" text-teal-500 text-center font-semibold">
                        Регистрация спецтехники
                    </p>
                </div>
                <div class="mt-2">
                    Вид спецтехники:
                    <select bind:value={carCategory}>
                        <option value=""></option>
                        <option value="Строительная">Строительная</option>
                        <option value="Дорожная">Дорожная</option>
                        <option value="Транспортная">Транспортная</option>
                        <option value="Коммунальная">Коммунальная</option>
                        <option value="Грузовая">Грузовая</option>
                        <option value="Сельхозтехника">Сельхозтехника</option>
                        <option value="Автоспецтехника">Автоспецтехника</option>
                        <option value="Лесная">Лесная</option>
                        <option value="Карьерная">Карьерная</option>
                        <option value="Производственная">Производственная</option>
                    </select>
                </div>
                <div>
                    Тип спецтехники:
                    <select bind:value={carType}>
                        <option value=""></option>
                        <option value="Экскаватор">Экскаватор</option>
                        <option value="Бульдозер">Бульдозер</option>
                        <option value="Трактор">Трактор</option>
                        <option value="Грейдер">Грейдер</option>
                        <option value="Самосвал">Самосвал</option>
                        <option value="Бетономешалка">Бетономешалка</option>
                        <option value="Бурильная">Бурильная техника</option>
                        <option value="Каток">Каток</option>
                        <option value="Ассенизатор">Ассенизатор</option>
                        <option value="Автокран">Автокран</option>
                        <option value="Маннипулятор">Маннипулятор</option>
                        <option value="Автоподъемник">Автоподъемник</option>
                        <option value="Погрузчик">Погрузчик</option>
                        <option value="Эвакуатор">Эвакуатор</option>
                        <option value="Другое">Другое</option>
                    </select>
                </div>
                {#if carType === 'Другое'}
                <div>
                    <input type="text" placeholder="Тип спецтехники" bind:value={anotherType}>
                </div>
                {/if}
                <div>
                    <input type="text" placeholder="Модель" bind:value={carModel} class="inputs">
                </div>
                <div>
                    <input type="number" placeholder="Стоимость аренды в час" bind:value={rentPrice} class="inputs">
                </div>
                <div>
                    <input type="text" placeholder="Номер телефона +7" bind:value={phone} class="inputs">
                </div>

                <div class="image-upload">
                    {#if carImageURL != undefined}

                    <img class="img-up" src={carImageURL} alt="carImage"/>

                    {:else}
                    <label for="carImage">
                                
                        <p class="mt-3 text-sm text-teal-500">
                            Сфотографируйте, а затем загрузите фото спецтехники
                        </p>

                        <img src="./icons/add_photo_teal.svg" alt="add_photo" class="img-icon">
                    </label>
                    <input type="file" id="carImage" name="file" on:change={(e) => carImageUpload(e, user.uid)}/>
                    {/if}   
                </div>

                <div class="mt-2 text-center">
                    <button class="btn-orange" on:click={regRentalCar}> 
                        Зарегистрировать
                    </button>
                </div>
            </div>
            {/if}
        </User>
    </FirebaseApp>
</div>


<!-- ALERT BOX -->
{#if alertBox}
    <div id="alertBox" class=" fixed top-0">

        <div transition:fade="{{ duration: 500 }}" class="p-3 border bg-white rounded-lg shadow-lg">

            <p class="text-center">
                {alertBoxContent}
            </p>

            <p class="mt-2 text-center">
                <button class="btn-orange" on:click={() => alertBox = false}>Ок</button>
            </p>

        </div>

    </div>
{/if}

<style>.inputs
{
  margin-top: 0.25rem;
  width: 100%;
}

.img-icon 
{
  margin-left: auto;
  margin-right: auto;
  padding: 0.5rem;
  width: 5rem;
  height: 5rem;
  text-align: center;
  cursor: pointer;
}

.image-upload 
{
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
  width: 16rem;
  height: 10rem;
  background-color: #f7fafc;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  text-align: center;
  overflow: hidden;
}

.img-up 
{
  height: 10rem;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.image-upload > input
{
  display: none;
}

/*# sourceMappingURL=data:application/json;base64, */</style>